olid $light-dark;\n  }\n}\n\n.sidebar-collapse .nav-sidebar .nav-header.nav-divider {\n  text-align: right;\n  &::before {\n    border-top: transparent!important;\n  }\n}\n\n.ravedash-back-to-top,\n.back-to-top {\n  // opacity: 0.4;\n  // transition: opacity 200ms;\n  bottom: 1.25rem;\n  position: fixed;\n  right: 1.25rem;\n  z-index: 1039;\n  box-shadow: 0 0 10px 1px adjust-color($gray, $alpha: -0.5);\n  border-radius: .25rem;\n\n  &.show,\n  &:hover {\n    opacity: 1;\n  }\n\n  & .dropdown-menu {\n    margin-bottom: 0;\n  }\n\n  &:focus {\n    box-shadow: none;\n  }\n\n  &.left {\n    right: auto;\n    left: 1.25rem;\n\n    & .rave-button-autorecalculate {\n      background-color: $yellow;\n      // adjust-color($color: $yellow, $lightness: 25%);\n    }\n  }\n}\n\n\n\n\n// code-display\n.info-box {\n  & .code-display {\n    @include padding($size: 0);\n    @include margin($size: -0.5, $unit: 1em);\n  }\n  & .info-box-content {\n    @include fill-width();\n  }\n}\n\n.code-display pre {\n  @include padding($size: 0);\n}\n\n.card-expanded .output-wrapper {\n  position: relative;\n  z-index: 1040;\n}\n\n.card-footer.code-display .info-box {\n  @include border-radius(0);\n}\n\n// card-accordion\n.card-accordion {\n  &.catd-tools {\n    @include position($type: absolute);\n    right: 0;\n    margin: 7px 5px; // TODO: change this\n  }\n  &.card-header {\n    @include padding($size: 0);\n    @include position($type: relative);\n\n    & .card-accordion-title {\n      @include padding($size: 10);\n    }\n\n    & [data-toggle=\"collapse\"] {\n      cursor: pointer;\n    }\n  }\n}\n\n// card2\n.card2-body {\n  @include overflow(hidden, x);\n  @include overflow(auto, y);\n\n  & .direct-chat-contacts {\n\n    background-color: adjust-color($gray-dark, $alpha: -0.5);\n\n    & hr {\n      @include border-color( rgb(232,232,232), top );\n    }\n  }\n}\n\n// group input box\n.group-input-box {\n  border: 1px dashed $light-gray;\n  padding-top: 0.6rem;\n  margin-top: 1.5rem;\n  margin-bottom: 1.5rem;\n  position: relative;\n\n  &::before {\n    content: attr(title);\n    position: absolute;\n    margin-top: -1.5rem;\n    left: 0.4rem;\n    color: $gray; // $light-dark;\n    background-color: $light;\n    padding: 0.1rem 0.4rem;\n  }\n}\n.dark-mode .group-input-box {\n  border: 1px dashed $light-dark;\n  &::before {\n    color: $light-gray;\n    background-color: $light-dark;\n  }\n}\n\n// progress-bar\n.progress-error { @include display(none, true); }\n.progress-message { @include display(block); }\n.shidashi-progress-error {\n  & .progress-error {\n    @include display(block, true);\n    color: $color-error !important;\n  }\n  & .progress-message {\n    @include display(none);\n  }\n  & .progress-bar {\n    background-color: $color-error !important;\n  }\n}\n\n// flip-box\n.flip-box {\n  background-color: transparent;\n  border: none;\n  perspective: 1000px;\n  padding: 0 !important;\n  position: relative;\n\n  &[data-toggle=\"click\"] .flip-box-front,\n  &[data-toggle=\"click-front\"] .flip-box-front {\n    cursor: pointer;\n  }\n\n  & .flip-box-inner {\n    position: relative;\n    width: 100%;\n    margin: 0 !important;\n    padding: 0 !important;\n    transition: transform 0.5s;\n    transition-timing-function: cubic-bezier(1, 0, 1, 1);\n    transform-style: preserve-3d;\n\n    & .flip-box-front,\n    & .flip-box-back {\n      width: 100%;\n      margin: 0 !important;\n      padding: 0 !important;\n      -webkit-backface-visibility: hidden;\n      backface-visibility: hidden;\n    }\n    & .flip-box-front {\n    \tposition: relative;\n      transform: rotateY(360deg);\n    }\n    & .flip-box-back {\n      position: absolute;\n      z-index: 1038;\n      transform: rotateY(180deg);\n    }\n  }\n\n  &.fill .flip-box-inner,\n  &.fill .flip-box-front {\n    @include fill-height();\n    @include fill-width();\n  }\n\n  &.active {\n    z-index: 1;\n  }\n  &.active .flip-box-inner {\n    transform: rotateY(180deg);\n  }\n}\n\n.card-body .flip-bo