ar-nav > .user-menu {\n  > .nav-link::after {\n    content: none;\n  }\n\n  > .dropdown-menu {\n    @include border-top-radius(0);\n    padding: 0;\n    width: 280px;\n\n    &,\n    > .user-body {\n      @include border-bottom-radius(4px);\n    }\n\n    // Header menu\n    > li.user-header {\n      height: 175px;\n      padding: 10px;\n      text-align: center;\n\n      // User image\n      > img {\n        z-index: 5;\n        height: 90px;\n        width: 90px;\n        border: 3px solid;\n        border-color: transparent;\n        border-color: rgba(255, 255, 255, .2);\n      }\n\n      > p {\n        z-index: 5;\n        font-size: 17px;\n        //text-shadow: 2px 2px 3px #333333;\n        margin-top: 10px;\n\n        > small {\n          display: block;\n          font-size: 12px;\n        }\n      }\n    }\n\n    // Menu Body\n    > .user-body {\n      @include clearfix ();\n      border-bottom: 1px solid $gray-700;\n      border-top: 1px solid $gray-300;\n      padding: 15px;\n\n      a {\n        @include media-breakpoint-up(sm) {\n          background-color: $white !important;\n          color: $gray-700 !important;\n        }\n      }\n    }\n\n    // Menu Footer\n    > .user-footer {\n      @include clearfix ();\n      background-color: $gray-100;\n      padding: 10px;\n\n      .btn-default {\n        color: $gray-600;\n\n        &:hover {\n          @include media-breakpoint-up(sm) {\n            background-color: $gray-100;\n          }\n        }\n      }\n    }\n  }\n\n  .user-image {\n    @include media-breakpoint-up(sm) {\n      float: none;\n      line-height: 10px;\n      margin-right: .4rem;\n      margin-top: -8px;\n    }\n\n    border-radius: 50%;\n    float: left;\n    height: $sidebar-user-image-width;\n    margin-right: 10px;\n    margin-top: -2px;\n    width: $sidebar-user-image-width;\n  }\n}\n\n@include dark-mode () {\n  .dropdown-menu {\n    background-color: $dark;\n    color: $white;\n  }\n  .dropdown-item {\n    color: $white;\n\n    &:focus,\n    &:hover {\n      background-color: lighten($dark, 5%);\n    }\n  }\n  .dropdown-divider {\n    border-color: $gray-600;\n  }\n\n  .navbar-nav > .user-menu > .dropdown-menu {\n    > .user-footer {\n      background-color: lighten($dark, 2.5%);\n      color: $white;\n\n      .btn-default {\n        color: $white;\n\n        &:hover,\n        &:focus {\n          background-color: lighten($dark, 5%);\n          color: $gray-300;\n        }\n        &:focus {\n          background-color: lighten($dark, 7.5%);\n        }\n      }\n    }\n    > .user-body {\n      border-color: $gray-600;\n    }\n    > .user-body a {\n      background-color: transparent !important;\n      color: $white !important;\n\n      &:hover,\n      &:focus {\n        color: $gray-400 !important;\n      }\n    }\n  }\n}\n","//\n// Component: Nav\n//\n\n.nav-pills {\n  .nav-link {\n    color: $gray-600;\n\n    &:not(.active):hover {\n      color: theme-color(\"primary\");\n    }\n  }\n\n  .nav-item {\n    &.dropdown.show {\n      .nav-link:hover {\n        color: $dropdown-link-active-color;\n      }\n    }\n  }\n}\n\n// Vertical Tabs\n.nav-tabs.flex-column {\n  border-bottom: 0;\n  border-right: $nav-tabs-border-width solid $nav-tabs-border-color;\n\n  .nav-link {\n    border-bottom-left-radius: $nav-tabs-border-radius;\n    border-top-right-radius: 0;\n    margin-right: -$nav-tabs-border-width;\n\n    @include hover-focus () {\n      border-color: $gray-200 transparent $gray-200 $gray-200;\n    }\n  }\n\n  .nav-link.active,\n  .nav-item.show .nav-link {\n    border-color: $gray-300 transparent $gray-300 $gray-300;\n  }\n\n  &.nav-tabs-right {\n    border-left: $nav-tabs-border-width solid $nav-tabs-border-color;\n    border-right: 0;\n\n    .nav-link {\n      border-bottom-left-radius: 0;\n      border-bottom-right-radius: $nav-tabs-border-radius;\n      border-top-left-radius: 0;\n      border-top-right-radius: $nav-tabs-border-radius;\n      margin-left: -$nav-tabs-border-width;\n\n      @include hover-focus () {\n        border-color: $gray-200 $gray-200 $gray-200 transparent;\n      }\n    }\n\n    .nav-link.active,\n    .nav-item.show .nav-link {\n      border-color: $gray-300 $gray-300 $gray-300 transparent;\n    }\n  }\n}\n\n.navbar-no-expand {\n  flex-direction: row;\n\n  .nav-link {\n    padding-left: $navbar-nav-link-padding-x;\n    padding-right: $navbar-nav-link-padding-x;\n  }\n\n  .dropdown-menu {\n    position: absolute;\n  }\n}\n\n// Color variants\n@each $color, $value in $theme-colors {\n  @if $color == dark or $color == light {\n    .navbar-#{$color} {\n      background-color: $value;\n      @if $color == dark {\n        border-color: lighten($dark, 10%);\n      }\n    }\n  }\n}\n\n@each $color, $value in $theme-colors {\n  @if $color != dark and $color != light {\n    @include navbar-variant($color, $value);\n  }\n}\n\n@each $color, $value in $colors {\n  @include navbar-variant($color, $value);\n}\n\n.navbar-nav-not-expanded {\n  flex-direction: row;\n\n  .dropdown-menu {\n    position: absolute;\n  }\n\n  .nav-link {\n    padding-right: $navbar-nav-link-padding-x;\n    padding-left: $navbar-nav-link-padding-x;\n  }\n}\n\n\n@include dark-mode () {\n  .nav-pills .nav-link {\n    color: $gray-400;\n  }\n\n  .nav-tabs {\n    border-color: lighten($dark, 15%);\n\n    .nav-link:focus,\n    .nav-link:hover {\n      border-color: lighten($dark, 15%);\n    }\n\n    .nav-item.show .nav-link,\n    .nav-link.active {\n      background-color: $dark;\n      border-color: lighten($dark, 15%) lighten($dark, 15%) transparent lighten($dark, 15%);\n      color: $white;\n    }\n\n    &.flex-column {\n      .nav-item.show .nav-link,\n      .nav-link {\n        &.active,\n        &:focus,\n        &:hover {\n          border-color: lighten($dark, 15%) transparent lighten($dark, 15%) lighten($dark, 15%);\n        }\n        &:focus,\n        &:hover {\n          background-color: lighten($dark, 5%);\n        }\n      }\n      &.nav-tabs-right {\n        border-color: lighten($dark, 15%);\n        .nav-link {\n          &.active,\n          &:focus,\n          &:hover {\n            border-color: lighten($dark, 15%) lighten($dark, 15%) lighten($dark, 15%) transparent;\n          }\n        }\n      }\n    }\n  }\n\n  // Color variants\n  @each $color, $value in $theme-colors-alt {\n    @if $color == dark or $color == light {\n      .navbar-#{$color} {\n        background-color: $value;\n        @if $color == dark {\n          border-color: lighten($dark, 10%);\n        }\n      }\n    }\n  }\n\n  @each $color, $value in $theme-colors-alt {\n    @if $color != dark and $color != light {\n      @include navbar-variant($color, $value);\n    }\n  }\n\n  @each $color, $value in $colors-alt {\n    @include navbar-variant($color, $value);\n  }\n}\n","//\n// Mixins: Navbar\n//\n\n// Navbar Variant\n@mixin navbar-variant($name, $color) {\n  .navbar-#{$name} {\n    background-color: $color;\n    color: color-yiq($color);\n\n    &.navbar-light {\n      .form-control-navbar {\n        &::placeholder {\n          color: rgba($gray-800, .8);\n        }\n        &,\n        + .input-group-append > .btn-navbar {\n          background-color: darken($color, 4%);\n          border-color: darken($color, 9%);\n          color: rgba($gray-800, .8);\n        }\n\n        &:focus {\n          &::placeholder {\n            color: $gray-800;\n          }\n          &,\n          + .input-group-append .btn-navbar {\n            background-color: darken($color, 5%);\n            border-color: darken($color, 9%) !important;\n            color: $gray-800;\n          }\n        }\n      }\n    }\n    &.navbar-dark {\n      .form-control-navbar {\n        &::placeholder {\n          color: rgba($white, .8);\n        }\n        &,\n        + .input-group-append > .btn-navbar {\n          background-color: lighten($color, 4%);\n          border-color: lighten($color, 9%);\n          color: rgba($white, .8);\n        }\n\n        &:focus {\n          &::placeholder {\n            color: $white;\n          }\n          &,\n          + .input-group-append .btn-navbar {\n            background-color: lighten($color, 5%);\n            border-color: lighten($color, 9%) !important;\n            color: $white;\n          }\n        }\n      }\n    }\n  }\n}\n","//\n// Component: Pagination\n//\n\n.pagination-month {\n  .page-item {\n    justify-self: stretch;\n\n    .page-link {\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      flex-direction: column;\n      box-shadow: none;\n    }\n    &:first-child,\n    &:last-child {\n      .page-link {\n        height: 100%;\n        font-size: $font-size-lg;\n      }\n    }\n    .page-month {\n      margin-bottom: 0;\n      font-size: $font-size-lg;\n      font-weight: $font-weight-bold;\n    }\n    .page-year {\n      margin-bottom: 0;\n    }\n  }\n\n  &.pagination-lg {\n    .page-month {\n      font-size: ($font-size-lg * 1.25);\n    }\n  }\n  &.pagination-sm {\n    .page-month {\n      font-size: ($font-size-base);\n    }\n  }\n}\n\n@include dark-mode () {\n  .page-item {\n    &.disabled a,\n    &.disabled .page-link {\n      background-color: lighten($dark, 2.5%) !important;\n      border-color: $gray-600 !important;\n      color: $gray-600;\n    }\n\n    .page-link {\n      color: $primary-alt;\n    }\n\n    &.active {\n      .page-link {\n        background-color: $primary-alt;\n        border-color: $primary-alt;\n        color: $white;\n\n        &:hover,\n        &:focus {\n          color: $gray-400 !important;\n        }\n      }\n    }\n    &:not(.active) {\n      .page-link {\n        background-color: $dark;\n        border-color: $gray-600;\n\n        &:hover,\n        &:focus {\n          color: lighten($primary-alt, 5%);\n          background-color: lighten($dark, 5%);\n        }\n      }\n    }\n  }\n}\n","//\n// Component: Forms\n//\n\n.form-group {\n  &.has-icon {\n    position: relative;\n\n    .form-control {\n      padding-right: 35px;\n    }\n\n    .form-icon {\n      background-color: transparent;\n      border: 0;\n      cursor: pointer;\n      font-size: 1rem;\n      // margin-top: -3px;\n      padding: $input-btn-padding-y $input-btn-padding-x;\n      position: absolute;\n      right: 3px;\n      top: 0;\n    }\n  }\n}\n\n// Button groups\n.btn-group-vertical {\n  .btn {\n    &.btn-flat:first-of-type,\n    &.btn-flat:last-of-type {\n      @include border-radius(0);\n    }\n  }\n}\n\n// Support icons in form-control\n.form-control-feedback {\n  &.fa,\n  &.fas,\n  &.far,\n  &.fab,\n  &.fal,\n  &.fad,\n  &.svg-inline--fa,\n  &.ion {\n    line-height: $input-height;\n  }\n}\n\n.input-lg + .form-control-feedback,\n.input-group-lg + .form-control-feedback {\n  &.fa,\n  &.fas,\n  &.far,\n  &.fab,\n  &.fal,\n  &.fad,\n  &.svg-inline--fa,\n  &.ion {\n    line-height: $input-height-lg;\n  }\n}\n\n.form-group-lg {\n  .form-control + .form-control-feedback {\n    &.fa,\n    &.fas,\n    &.far,\n    &.fab,\n    &.fal,\n    &.fad,\n    &.svg-inline--fa,\n    &.ion {\n      line-height: $input-height-lg;\n    }\n  }\n}\n\n.input-sm + .form-control-feedback,\n.input-group-sm + .form-control-feedback {\n  &.fa,\n  &.fas,\n  &.far,\n  &.fab,\n  &.fal,\n  &.fad,\n  &.svg-inline--fa,\n  &.ion {\n    line-height: $input-height-sm;\n  }\n}\n\n.form-group-sm {\n  .form-control + .form-control-feedback {\n    &.fa,\n    &.fas,\n    &.far,\n    &.fab,\n    &.fal,\n    &.fad,\n    &.svg-inline--fa,\n    &.ion {\n      line-height: $input-height-sm;\n    }\n  }\n}\n\nlabel:not(.form-check-label):not(.custom-file-label) {\n  font-weight: $font-weight-bold;\n}\n\n.warning-feedback {\n  @include font-size($form-feedback-font-size);\n  color: theme-color(\"warning\");\n  display: none;\n  margin-top: $form-feedback-margin-top;\n  width: 100%;\n}\n\n.warning-tooltip {\n  @include border-radius($form-feedback-tooltip-border-radius);\n  @include font-size($form-feedback-tooltip-font-size);\n  background-color: rgba(theme-color(\"warning\"), $form-feedback-tooltip-opacity);\n  color: color-yiq(theme-color(\"warning\"));\n  display: none;\n  line-height: $form-feedback-tooltip-line-height;\n  margin-top: .1rem;\n  max-width: 100%; // Contain to parent when possible\n  padding: $form-feedback-tooltip-padding-y $form-feedback-tooltip-padding-x;\n  position: absolute;\n  top: 100%;\n  z-index: 5;\n}\n\n.form-control {\n  &.is-warning {\n    border-color: theme-color(\"warning\");\n\n    @if $enable-validation-icons {\n      // padding-right: $input-height-inner;\n      // background-image: none;\n      // background-repeat: no-repeat;\n      // background-position: center right $input-height-inner-quarter;\n      // background-size: $input-height-inner-half $input-height-inner-half;\n    }\n\n    &:focus {\n      border-color: theme-color(\"warning\");\n      box-shadow: 0 0 0 $input-focus-width rgba(theme-color(\"warning\"), .25);\n    }\n\n    ~ .warning-feedback,\n    ~ .warning-tooltip {\n      display: block;\n    }\n  }\n}\n\n// stylelint-disable-next-line selector-no-qualifying-type\ntextarea.form-control {\n  &.is-warning {\n    @if $enable-validation-icons {\n      padding-right: $input-height-inner;\n      background-position: top $input-height-inner-quarter right $input-height-inner-quarter;\n    }\n  }\n}\n\n.custom-select {\n  &.is-warning {\n    border-color: theme-color(\"warning\");\n\n    @if $enable-validation-icons {\n      // padding-right: $custom-select-feedback-icon-padding-right;\n      // background: $custom-select-background, none $custom-select-bg no-repeat $custom-select-feedback-icon-position / $custom-select-feedback-icon-size;\n    }\n\n    &:focus {\n      border-color: theme-color(\"warning\");\n      box-shadow: 0 0 0 $input-focus-width rgba(theme-color(\"warning\"), .25);\n    }\n\n    ~ .warning-feedback,\n    ~ .warning-tooltip {\n      display: block;\n    }\n  }\n}\n\n\n.form-control-file {\n  &.is-warning {\n    ~ .warning-feedback,\n    ~ .warning-tooltip {\n      display: block;\n    }\n  }\n}\n\n.form-check-input {\n  &.is-warning {\n    ~ .form-check-label {\n      color: theme-color(\"warning\");\n    }\n\n    ~ .warning-feedback,\n    ~ .warning-tooltip {\n      display: block;\n    }\n  }\n}\n\n.custom-control-input.is-warning {\n  ~ .custom-control-label {\n    color: theme-color(\"warning\");\n\n    &::before {\n      border-color: theme-color(\"warning\");\n    }\n  }\n\n  ~ .warning-feedback,\n  ~ .warning-tooltip {\n    display: block;\n  }\n\n  &:checked {\n    ~ .custom-control-label::before {\n      @include gradient-bg(lighten(theme-color(\"warning\"), 10%));\n      border-color: lighten(theme-color(\"warning\"), 10%);\n    }\n  }\n\n  &:focus {\n    ~ .custom-control-label::before {\n      box-shadow: 0 0 0 $input-focus-width rgba(theme-color(\"warning\"), .25);\n    }\n\n    &:not(:checked) ~ .custom-control-label::before {\n      border-color: theme-color(\"warning\");\n    }\n  }\n}\n\n// custom file\n.custom-file-input {\n  &.is-warning {\n    ~ .custom-file-label {\n      border-color: theme-color(\"warning\");\n    }\n\n    ~ .warning-feedback,\n    ~ .warning-tooltip {\n      display: block;\n    }\n\n    &:focus {\n      ~ .custom-file-label {\n        border-color: theme-color(\"warning\");\n        box-shadow: 0 0 0 $input-focus-width rgba(theme-color(\"warning\"), .25);\n      }\n    }\n  }\n}\n\n// body.text-sm support\nbody.text-sm {\n  .input-group-text {\n    font-size: $font-size-sm;\n  }\n}\n\n// custom .form-control styles\n.form-control,\n.custom-select {\n  &.form-control-border {\n    border-top: 0;\n    border-left: 0;\n    border-right: 0;\n    border-radius: 0;\n    box-shadow: inherit;\n\n    &.border-width-2 {\n      border-bottom-width: 2px;\n    }\n    &.border-width-3 {\n      border-bottom-width: 3px;\n    }\n  }\n}\n\n// custom switch color variations\n.custom-switch {\n  @each $name, $color in $theme-colors {\n    @include custom-switch-variant($name, $color);\n  }\n\n  @each $name, $color in $colors {\n    @include custom-switch-variant($name, $color);\n  }\n}\n\n// custom range color variations\n.custom-range {\n  @each $name, $color in $theme-colors {\n    @include custom-range-variant($name, $color);\n  }\n\n  @each $name, $color in $colors {\n    @include custom-range-variant($name, $color);\n  }\n}\n\n// custom control input variations\n@each $name, $color in $theme-colors {\n  @include custom-control-input-variant($name, $color);\n}\n\n@each $name, $color in $colors {\n  @include custom-control-input-variant($name, $color);\n}\n\n.custom-control-input-outline {\n  ~ .custom-control-label::before {\n    background-color: transparent !important;\n    box-shadow: none;\n  }\n  &:checked ~ .custom-control-label::before {\n    @include gradient-bg(transparent);\n  }\n}\n\n.navbar-dark {\n  .btn-navbar,\n  .form-control-navbar {\n    background-color: lighten($sidebar-dark-bg, 5%);\n    border: 1px solid lighten($sidebar-dark-bg, 15%);\n    color: lighten(color-yiq(lighten($sidebar-dark-bg, 5%)), 15%);\n  }\n  .btn-navbar {\n    &:hover {\n      background-color: lighten($sidebar-dark-bg, 7.5%);\n    }\n    &:focus {\n      background-color: lighten($sidebar-dark-bg, 10%);\n    }\n  }\n\n  .form-control-navbar + .input-group-prepend,\n  .form-control-navbar + .input-group-append {\n    > .btn-navbar {\n      background-color: lighten($sidebar-dark-bg, 5%);\n      color: $white;\n      border: 1px solid lighten($sidebar-dark-bg, 15%);\n      border-left: none;\n    }\n  }\n}\n\n@include dark-mode () {\n  .form-control:not(.form-control-navbar):not(.form-control-sidebar),\n  .custom-select,\n  .custom-file-label,\n  .custom-file-label::after,\n  .custom-control-label::before,\n  .input-group-text {\n    background-color: $dark;\n    color: $white;\n  }\n  .form-control:not(.form-control-navbar):not(.form-control-sidebar):not(.is-invalid):not(:focus),\n  .custom-file-label,\n  .custom-file-label::after {\n    border-color: $gray-600;\n  }\n  select {\n    background-color: $dark;\n    color: $white;\n    border-color: $gray-600;\n  }\n\n  .custom-select {\n    background: $dark $custom-select-dark-background;\n\n    &[multiple]{\n      background: $dark;\n    }\n  }\n\n  .input-group-text {\n    border-color: $gray-600;\n  }\n\n  .custom-control-input:disabled ~ .custom-control-label::before,\n  .custom-control-input[disabled] ~ .custom-control-label::before {\n    background-color: lighten($dark, 5%);\n    border-color: $gray-600;\n    color: $white;\n  }\n\n  input:-webkit-autofill,\n  input:-webkit-autofill:hover,\n  input:-webkit-autofill:focus,\n  textarea:-webkit-autofill,\n  textarea:-webkit-autofill:hover,\n  textarea:-webkit-autofill:focus,\n  select:-webkit-autofill,\n  select:-webkit-autofill:hover,\n  select:-webkit-autofill:focus {\n    -webkit-text-fill-color: $white;\n  }\n\n  .custom-range {\n    &::-webkit-slider-runnable-track {\n      background-color: lighten($dark, 7.5%);\n    }\n    &::-moz-range-track {\n      background-color: lighten($dark, 7.5%);\n    }\n    &::-ms-track {\n      background-color: lighten($dark, 7.5%);\n    }\n\n    @each $name, $color in $theme-colors-alt {\n      @include custom-range-variant($name, $color);\n    }\n\n    @each $name, $color in $colors-alt {\n      @include custom-range-variant($name, $color);\n    }\n  }\n\n  // custom switch color variations\n  .custom-switch {\n    @each $name, $color in $theme-colors-alt {\n      @include custom-switch-variant($name, $color);\n    }\n\n    @each $name, $color in $colors-alt {\n      @include custom-switch-variant($name, $color);\n    }\n  }\n\n  @each $name, $color in $theme-colors-alt {\n    @include custom-control-input-variant($name, $color);\n  }\n\n  @each $name, $color in $colors-alt {\n    @include custom-control-input-variant($name, $color);\n  }\n}\n","//\n// Mixins: Custom Forms\n//\n\n// Custom Switch Variant\n@mixin custom-switch-variant($name, $color) {\n  &.custom-switch-off-#{$name} {\n    .custom-control-input ~ .custom-control-label::before {\n      background-color: #{$color};\n      border-color: darken($color, 20%);\n    }\n\n    .custom-control-input:focus ~ .custom-control-label::before {\n      box-shadow: 0 0 0 1px $body-bg, 0 0 0 2px rgba($color, .25);\n    }\n\n    .custom-control-input ~ .custom-control-label::after {\n      background-color: darken($color, 25%);\n    }\n  }\n\n  &.custom-switch-on-#{$name} {\n    .custom-control-input:checked ~ .custom-control-label::before {\n      background-color: #{$color};\n      border-color: darken($color, 20%);\n    }\n\n    .custom-control-input:checked:focus ~ .custom-control-label::before {\n      box-shadow: 0 0 0 1px $body-bg, 0 0 0 2px rgba($color, .25);\n    }\n\n    .custom-control-input:checked ~ .custom-control-label::after {\n      background-color: lighten($color, 30%);\n    }\n  }\n}\n\n// Custom Range Variant\n@mixin custom-range-variant($name, $color) {\n  &.custom-range-#{$name} {\n    &:focus {\n      outline: none;\n\n      &::-webkit-slider-thumb {\n        box-shadow: 0 0 0 1px $body-bg, 0 0 0 2px rgba($color, .25);\n      }\n\n      &::-moz-range-thumb     {\n        box-shadow: 0 0 0 1px $body-bg, 0 0 0 2px rgba($color, .25);\n      }\n\n      &::-ms-thumb            {\n        box-shadow: 0 0 0 1px $body-bg, 0 0 0 2px rgba($color, .25);\n      }\n    }\n\n    &::-webkit-slider-thumb {\n      background-color: $color;\n\n      &:active {\n        background-color: lighten($color, 35%);\n      }\n    }\n\n    &::-moz-range-thumb {\n      background-color: $color;\n\n      &:active {\n        background-color: lighten($color, 35%);\n      }\n    }\n\n    &::-ms-thumb {\n      background-color: $color;\n\n      &:active {\n        background-color: lighten($color, 35%);\n      }\n    }\n  }\n}\n\n\n// Custom Control Input Variant\n@mixin custom-control-input-variant($name, $color) {\n  $custom-control-indicator-checked-color: $color;\n  $custom-checkbox-indicator-icon-checked: str-replace(url(\"data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3E%3Cpath fill='#{$custom-control-indicator-checked-color}' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3E%3C/svg%3E\"), \"#\", \"%23\");\n  $custom-radio-indicator-icon-checked: str-replace(url(\"data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3E%3Ccircle r='3' fill='#{$custom-control-indicator-checked-color}'/%3E%3C/svg%3E\"), \"#\", \"%23\");\n\n  .custom-control-input-#{$name} {\n    &:checked ~ .custom-control-label::before {\n      border-color: $color;\n      @include gradient-bg($color);\n    }\n\n    &.custom-control-input-outline:checked {\n      &[type=\"checkbox\"] ~ .custom-control-label::after {\n        background-image: $custom-checkbox-indicator-icon-checked !important;\n      }\n      &[type=\"radio\"] ~ .custom-control-label::after {\n        background-image: $custom-radio-indicator-icon-checked !important;\n      }\n    }\n\n    &:focus ~ .custom-control-label::before {\n      // the mixin is not used here to make sure there is feedback\n      @if $enable-shadows {\n        box-shadow: $input-box-shadow, 0 0 0 $input-btn-focus-width rgba($color, .25);\n      } @else {\n        box-shadow: 0 0 0 $input-btn-focus-width rgba($color, .25);\n      }\n    }\n\n    &:focus:not(:checked) ~ .custom-control-label::before {\n      border-color: lighten($color, 25%);\n    }\n\n    &:not(:disabled):active ~ .custom-control-label::before {\n      background-color: lighten($color, 35%);\n      border-color: lighten($color, 35%);\n    }\n  }\n}\n","//\n// Component: Progress Bar\n//\n\n//General CSS\n.progress {\n  @include box-shadow(none);\n  @include border-radius($progress-bar-border-radius);\n\n  // Vertical bars\n  &.vertical {\n    display: inline-block;\n    height: 200px;\n    margin-right: 10px;\n    position: relative;\n    width: 30px;\n\n    > .progress-bar {\n      bottom: 0;\n      position: absolute;\n      width: 100%;\n    }\n\n    //Sizes\n    &.sm,\n    &.progress-sm {\n      width: 20px;\n    }\n\n    &.xs,\n    &.progress-xs {\n      width: 10px;\n    }\n\n    &.xxs,\n    &.progress-xxs {\n      width: 3px;\n    }\n  }\n}\n\n.progress-group {\n  margin-bottom: map-get($spacers, 2);\n}\n\n// size variation\n.progress-sm {\n  height: 10px;\n}\n\n.progress-xs {\n  height: 7px;\n}\n\n.progress-xxs {\n  height: 3px;\n}\n\n// Remove margins from progress bars when put in a table\n.table {\n  tr > td {\n    .progress {\n      margin: 0;\n    }\n  }\n}\n\n@include dark-mode () {\n  .progress {\n    background: lighten($dark, 7.5%);\n  }\n}\n","//\n// Mixins: Cards Variant\n//\n\n@mixin cards-variant($name, $color) {\n  .card-#{$name} {\n    &:not(.card-outline) {\n      > .card-header {\n        background-color: $color;\n\n        &,\n        a {\n          color: color-yiq($color);\n        }\n\n        a.active {\n          color: color-yiq($white);\n        }\n      }\n    }\n\n    &.card-outline {\n      border-top: 3px solid $color;\n    }\n\n    &.card-outline-tabs {\n      > .card-header {\n        a:hover{\n          border-top: 3px solid $nav-tabs-border-color;\n        }\n        a.active,\n        a.active:hover{\n          border-top: 3px solid $color;\n        }\n      }\n    }\n  }\n\n  .bg-#{$name},\n  .bg-gradient-#{$name},\n  .card-#{$name}:not(.card-outline) {\n    > .card-header {\n      .btn-tool {\n        color: rgba(color-yiq($color), .8);\n\n        &:hover {\n          color: color-yiq($color);\n        }\n      }\n    }\n  }\n\n  .card.bg-#{$name},\n  .card.bg-gradient-#{$name} {\n    .bootstrap-datetimepicker-widget {\n      .table td,\n      .table th {\n        border: none;\n      }\n\n      table thead tr:first-child th:hover,\n      table td.day:hover,\n      table td.hour:hover,\n      table td.minute:hover,\n      table td.second:hover {\n        background-color: darken($color, 8%);\n        color: color-yiq($color);\n      }\n\n      table td.today::before {\n        border-bottom-color: color-yiq($color);\n      }\n\n      table td.active,\n      table td.active:hover {\n        background-color: lighten($color, 10%);\n        color: color-yiq($color);\n      }\n    }\n  }\n}\n\n","//\n// Component: Cards\n//\n\n// Color variants\n@each $name, $color in $theme-colors {\n  @include cards-variant($name, $color);\n}\n\n@each $name, $color in $colors {\n  @include cards-variant($name, $color);\n}\n\n.card {\n  @include box-shadow($card-shadow);\n  margin-bottom: map-get($spacers, 3);\n\n  &.bg-dark {\n    .card-header {\n      border-color: $card-dark-border-color;\n    }\n\n    &,\n    .card-body {\n      color: $white;\n    }\n  }\n\n  &.maximized-card {\n    height: 100% !important;\n    left: 0;\n    max-height: 100% !important;\n    max-width: 100% !important;\n    position: fixed;\n    top: 0;\n    width: 100% !important;\n    z-index: $zindex-modal-backdrop;\n\n    &.was-collapsed .card-body {\n      display: block !important;\n    }\n\n    .card-body {\n      overflow: auto;\n    }\n\n    [data-card-widgett=\"collapse\"] {\n      display: none;\n    }\n\n    .card-header,\n    .card-footer {\n      @include border-radius(0 !important);\n    }\n  }\n\n  // collapsed mode\n  &.collapsed-card {\n    .card-body,\n    .card-footer {\n      display: none;\n    }\n  }\n\n  .nav.flex-column:not(.nav-sidebar) {\n    > li {\n      border-bottom: 1px solid $card-border-color;\n      margin: 0;\n\n      &:last-of-type {\n        border-bottom: 0;\n      }\n    }\n  }\n\n  // fixed height to 300px\n  &.height-control {\n    .card-body {\n      max-height: 300px;\n      overflow: auto;\n    }\n  }\n\n  .border-right {\n    border-right: 1px solid $card-border-color;\n  }\n\n  .border-left {\n    border-left: 1px solid $card-border-color;\n  }\n\n  &.card-tabs {\n    &:not(.card-outline) {\n      > .card-header {\n        border-bottom: 0;\n\n        .nav-item {\n          &:first-child .nav-link {\n            border-left-color: transparent;\n          }\n        }\n      }\n    }\n\n    &.card-outline {\n      .nav-item {\n        border-bottom: 0;\n\n        &:first-child .nav-link {\n          border-left: 0;\n          margin-left: 0;\n        }\n      }\n    }\n\n    .card-tools {\n      margin: .3rem .5rem;\n    }\n\n    &:not(.expanding-card).collapsed-card {\n      .card-header {\n        border-bottom: 0;\n\n        .nav-tabs {\n          border-bottom: 0;\n\n          .nav-item {\n            margin-bottom: 0;\n          }\n        }\n      }\n    }\n\n    &.expanding-card {\n      .card-header {\n        .nav-tabs {\n          .nav-item {\n            margin-bottom: -1px;\n          }\n        }\n      }\n    }\n  }\n\n  &.card-outline-tabs {\n    border-top: 0;\n\n    .card-header {\n      .nav-item {\n        &:first-child .nav-link {\n          border-left: 0;\n          margin-left: 0;\n        }\n      }\n\n      a {\n        border-top: 3px solid transparent;\n\n        &:hover {\n          border-top: 3px solid $nav-tabs-border-color;\n        }\n\n        &.active {\n          &:hover {\n            margin-top: 0;\n          }\n        }\n      }\n    }\n\n    .card-tools {\n      margin: .5rem .5rem .3rem;\n    }\n\n    &:not(.expanding-card).collapsed-card .card-header {\n      border-bottom: 0;\n\n      .nav-tabs {\n        border-bottom: 0;\n\n        .nav-item {\n          margin-bottom: 0;\n        }\n      }\n    }\n\n    &.expanding-card {\n      .card-header {\n        .nav-tabs {\n          .nav-item {\n            margin-bottom: -1px;\n          }\n        }\n      }\n    }\n  }\n\n}\n\n// Maximized Card Body Scroll fix\nhtml.maximized-card {\n  overflow: hidden;\n}\n\n// Add clearfix to header, body and footer\n.card-header,\n.card-body,\n.card-footer {\n  @include clearfix ();\n}\n\n// Box header\n.card-header {\n  background-color: transparent;\n  border-bottom: 1px solid $card-border-color;\n  padding: (($card-spacer-y * .5) * 2) $card-spacer-x;\n  position: relative;\n\n  @if $enable-rounded {\n    @include border-top-radius($border-radius);\n  }\n\n  .collapsed-card & {\n    border-bottom: 0;\n  }\n\n  > .card-tools {\n    float: right;\n    margin-right: -$card-spacer-x * .5;\n\n    .input-group,\n    .nav,\n    .pagination {\n      margin-bottom: -$card-spacer-y * .4;\n      margin-top: -$card-spacer-y * .4;\n    }\n\n    [data-toggle=\"tooltip\"] {\n      position: relative;\n    }\n  }\n}\n\n.card-title {\n  float: left;\n  font-size: $card-title-font-size;\n  font-weight: $card-title-font-weight;\n  margin: 0;\n}\n\n.card-text {\n  clear: both;\n}\n\n\n// Box Tools Buttons\n.btn-tool {\n  background-color: transparent;\n  color: $gray-500;\n  font-size: $font-size-sm;\n  margin: -(($card-spacer-y * .5) * 2) 0;\n  padding: .25rem .5rem;\n\n  .btn-group.show &,\n  &:hover {\n    color: $gray-700;\n  }\n\n  .show &,\n  &:focus {\n    box-shadow: none !important;\n  }\n}\n\n.text-sm {\n  .card-title {\n    font-size: $card-title-font-size-sm;\n  }\n\n  .nav-link {\n    padding: $card-nav-link-padding-sm-y $card-nav-link-padding-sm-x;\n  }\n}\n\n// Box Body\n.card-body {\n  // @include border-radius-sides(0, 0, $border-radius, $border-radius);\n  // .no-header & {\n  //   @include border-top-radius($border-radius);\n  // }\n\n  // Tables within the box body\n  > .table {\n    margin-bottom: 0;\n\n    > thead > tr > th,\n    > thead > tr > td {\n      border-top-width: 0;\n    }\n  }\n\n  // Calendar within the box body\n  .fc {\n    margin-top: 5px;\n  }\n\n  .full-width-chart {\n    margin: -19px;\n  }\n\n  &.p-0 .full-width-chart {\n    margin: -9px;\n  }\n}\n\n.chart-legend {\n  @include list-unstyled ();\n  margin: 10px 0;\n\n  > li {\n    @media (max-width: map-get($grid-breakpoints, sm)) {\n      float: left;\n      margin-right: 10px;\n    }\n  }\n}\n\n// Comment Box\n.card-comments {\n  background-color: $gray-100;\n\n  .card-comment {\n    @include clearfix ();\n    border-bottom: 1px solid $gray-200;\n    padding: 8px 0;\n\n    &:last-of-type {\n      border-bottom: 0;\n    }\n\n    &:first-of-type {\n      padding-top: 0;\n    }\n\n    img {\n      height: $card-img-size;\n      width: $card-img-size;\n      float: left;\n    }\n  }\n\n  .comment-text {\n    color: lighten($gray-700, 20%);\n    margin-left: 40px;\n  }\n\n  .username {\n    color: $gray-700;\n    display: block;\n    font-weight: 600;\n  }\n\n  .text-muted {\n    font-size: 12px;\n    font-weight: 400;\n  }\n}\n\n// Widgets\n//-----------\n\n// Widget: TODO LIST\n.todo-list {\n  list-style: none;\n  margin: 0;\n  overflow: auto;\n  padding: 0;\n\n  // Todo list element\n  > li {\n    @include border-radius(2px);\n    background-color: $gray-100;\n    border-left: 2px solid $gray-200;\n    color: $gray-700;\n    margin-bottom: 2px;\n    padding: 10px;\n\n    &:last-of-type {\n      margin-bottom: 0;\n    }\n\n    > input[type=\"checkbox\"] {\n      margin: 0 10px 0 5px;\n    }\n\n    .text {\n      display: inline-block;\n      font-weight: 600;\n      margin-left: 5px;\n    }\n\n    // Time labels\n    .badge {\n      font-size: .7rem;\n      margin-left: 10px;\n    }\n\n    // Tools and options box\n    .tools {\n      color: theme-color(\"danger\");\n      display: none;\n      float: right;\n\n      // icons\n      > .fa,\n      > .fas,\n      > .far,\n      > .fab,\n      > .fal,\n      > .fad,\n      > .svg-inline--fa,\n      > .ion {\n        cursor: pointer;\n        margin-right: 5px;\n      }\n    }\n\n    &:hover .tools {\n      display: inline-block;\n    }\n\n    &.done {\n      color: darken($gray-500, 25%);\n\n      .text {\n        font-weight: 500;\n        text-decoration: line-through;\n      }\n\n      .badge {\n        background-color: $gray-500 !important;\n      }\n    }\n  }\n\n  // Color variants\n  @each $name, $color in $theme-colors {\n    .#{$name} {\n      border-left-color: $color;\n    }\n  }\n\n  @each $name, $color in $colors {\n    .#{$name} {\n      border-left-color: $color;\n    }\n  }\n\n  .handle {\n    cursor: move;\n    display: inline-block;\n    margin: 0 5px;\n  }\n}\n\n// END TODO WIDGET\n\n// Input in box\n.card-input {\n  max-width: 200px;\n}\n\n// Nav Tabs override\n.card-default {\n  .nav-item {\n    &:first-child .nav-link {\n      border-left: 0;\n    }\n  }\n}\n\n@include dark-mode () {\n\n  // Color variants\n  @each $name, $color in $theme-colors-alt {\n    @include cards-variant($name, $color);\n  }\n\n  @each $name, $color in $colors-alt {\n    @include cards-variant($name, $color);\n  }\n\n  .card {\n    background-color: $dark;\n    color: $white;\n\n    .card {\n      background-color: lighten($dark, 5%);\n      color: $white;\n    }\n    .nav.flex-column > li {\n      border-bottom-color: $gray-600;\n    }\n    .card-footer {\n      background-color: rgba($black, .1);\n    }\n    &.card-outline-tabs {\n      border-top: 0;\n    }\n    &.card-outline-tabs .card-header a:hover {\n      border-top-color: $gray-600;\n      border-bottom-color: transparent;\n    }\n    &:not(.card-outline) > .card-header a.active {\n      color: $white;\n    }\n  }\n\n  .card-comments {\n    background-color: lighten($dark, 1.25%);\n    .username {\n      color: $gray-400;\n    }\n    .card-comment {\n      border-bottom-color: lighten($dark, 7.5%);\n    }\n  }\n\n  .todo-list > li {\n    background-color: lighten($dark, 5%);\n    border-color: lighten($dark, 7.5%);\n    color: $white;\n  }\n\n  .todo-list {\n    @each $name, $color in $theme-colors-alt {\n      .#{$name} {\n        border-left-color: $color;\n      }\n    }\n\n    @each $name, $color in $colors-alt {\n      .#{$name} {\n        border-left-color: $color;\n      }\n    }\n  }\n}\n","//\n// Component: Modals\n//\n\n// Overlay\n.modal-dialog {\n  .overlay {\n    display: flex;\n    position: absolute;\n    left: 0;\n    top: 0;\n    bottom: 0;\n    right: 0;\n    margin: -$modal-content-border-width;\n    z-index: ($zindex-modal + 2);\n    justify-content: center;\n    align-items: center;\n    background-color: rgba($black, .7);\n    color: darken($gray-600, 2.5%);\n    @include border-radius($modal-content-border-radius);\n  }\n}\n\n\n// BG Color Variations Fixes\n.modal-content {\n  &.bg-warning {\n    .modal-header,\n    .modal-footer {\n      border-color: $gray-800;\n    }\n  }\n\n  &.bg-primary,\n  &.bg-secondary,\n  &.bg-info,\n  &.bg-danger,\n  &.bg-success, {\n    .close {\n      color: $white;\n      text-shadow: 0 1px 0 $black;\n    }\n  }\n}\n\n@include dark-mode () {\n  .modal-header,\n  .modal-footer {\n    border-color: $gray-600;\n  }\n  .modal-content {\n    background-color: $dark;\n\n    &.bg-warning {\n      .modal-header,\n      .modal-footer {\n        border-color: $gray-600;\n      }\n      .close {\n        color: $dark !important;\n        text-shadow: 0 1px 0 $gray-700 !important;\n      }\n    }\n\n    &.bg-primary,\n    &.bg-secondary,\n    &.bg-info,\n    &.bg-danger,\n    &.bg-success {\n      .modal-header,\n      .modal-footer {\n        border-color: $white;\n      }\n    }\n  }\n}\n","//\n// Component: Toasts\n//\n\n.toasts-top-right {\n  position: absolute;\n  right: 0;\n  top: 0;\n  z-index: $zindex-toasts;\n\n  &.fixed {\n    position: fixed;\n  }\n}\n\n.toasts-top-left {\n  left: 0;\n  position: absolute;\n  top: 0;\n  z-index: $zindex-toasts;\n\n  &.fixed {\n    position: fixed;\n  }\n}\n\n.toasts-bottom-right {\n  bottom: 0;\n  position: absolute;\n  right: 0;\n  z-index: $zindex-toasts;\n\n  &.fixed {\n    position: fixed;\n  }\n}\n\n.toasts-bottom-left {\n  bottom: 0;\n  left: 0;\n  position: absolute;\n  z-index: $zindex-toasts;\n\n  &.fixed {\n    position: fixed;\n  }\n}\n\n@include dark-mode () {\n  .toast {\n    background-color: rgba($dark, .85);\n    color: $white;\n\n    .toast-header {\n      background-color: rgba($dark, .7);\n      color: $gray-100;\n    }\n\n    @each $name, $color in $theme-colors-alt {\n      @include toast-variant($name, $color);\n    }\n\n    @each $name, $color in $colors-alt {\n      @include toast-variant($name, $color);\n    }\n  }\n}\n\n\n.toast {\n  @each $name, $color in $theme-colors {\n    @include toast-variant($name, $color);\n  }\n  @each $name, $color in $colors {\n    @include toast-variant($name, $color);\n  }\n}\n","//\n// Mixins: Toasts\n//\n\n// Toast Variant\n@mixin toast-variant($name, $color) {\n  &.bg-#{$name} {\n    background-color: rgba($color, .9) !important;\n    @if (color-yiq($color) == $yiq-text-light) {\n\n      .close {\n        color: color-yiq($color);\n        text-shadow: 0 1px 0 $black;\n      }\n    }\n\n    .toast-header {\n      background-color: rgba($color, .85);\n      color: color-yiq($color);\n    }\n  }\n}\n\n","//\n// Component: Button\n//\n\n.btn {\n  &.disabled,\n  &:disabled {\n    cursor: not-allowed;\n  }\n\n  // Flat buttons\n  &.btn-flat {\n    @include border-radius(0);\n    border-width: 1px;\n    box-shadow: none;\n  }\n\n  // input file btn\n  &.btn-file {\n    overflow: hidden;\n    position: relative;\n\n    > input[type=\"file\"] {\n      background-color: $white;\n      cursor: inherit;\n      display: block;\n      font-size: 100px;\n      min-height: 100%;\n      min-width: 100%;\n      opacity: 0;\n      outline: none;\n      position: absolute;\n      right: 0;\n      text-align: right;\n      top: 0;\n    }\n  }\n\n  .text-sm & {\n    font-size: $font-size-sm !important;\n  }\n}\n\n// Button color variations\n.btn-default {\n  background-color: $button-default-background-color;\n  border-color: $button-default-border-color;\n  color: $button-default-color;\n\n  &:hover,\n  &:active,\n  &.hover {\n    background-color: darken($button-default-background-color, 5%);\n    color: darken($button-default-color, 10%);\n  }\n\n  &.disabled,\n  &:disabled {\n    color: $button-default-color;\n    background-color: $button-default-background-color;\n  }\n}\n\n.btn-outline-light {\n  color: darken($light, 20%);\n  border-color: darken($light, 20%);\n\n  &.disabled,\n  &:disabled {\n    color: darken($light, 20%);\n    border-color: darken($light, 20%);\n  }\n}\n\n// Application buttons\n.btn-app {\n  @include border-radius(3px);\n  background-color: $button-default-background-color;\n  border: 1px solid $button-default-border-color;\n  color: $gray-600;\n  font-size: 12px;\n  height: 60px;\n  margin: 0 0 10px 10px;\n  min-width: 80px;\n  padding: 15px 5px;\n  position: relative;\n  text-align: center;\n\n  // Icons within the btn\n  > .fa,\n  > .fas,\n  > .far,\n  > .fab,\n  > .fal,\n  > .fad,\n  > .svg-inline--fa,\n  > .ion {\n    display: block;\n    font-size: 20px;\n  }\n\n  > .svg-inline--fa {\n    margin: 0 auto;\n  }\n\n  &:hover {\n    background-color: $button-default-background-color;\n    border-color: darken($button-default-border-color, 20%);\n    color: $button-default-color;\n  }\n\n  &:active,\n  &:focus {\n    @include box-shadow(inset 0 3px 5px rgba($black, .125));\n  }\n\n  // The badge\n  > .badge {\n    font-size: 10px;\n    font-weight: 400;\n    position: absolute;\n    right: -10px;\n    top: -3px;\n  }\n}\n\n// Extra Button Size\n\n.btn-xs {\n  @include button-size($button-padding-y-xs, $button-padding-x-xs, $button-font-size-xs, $button-line-height-xs, $button-border-radius-xs);\n}\n\n@include dark-mode () {\n  .btn-default,\n  .btn-app {\n    background-color: lighten($dark, 2.5%);\n    color: $white;\n    border-color: $gray-600;\n\n    &:hover,\n    &:focus {\n      background-color: lighten($dark, 5%);\n      color: $gray-300;\n      border-color: lighten($gray-600, 2.5%);\n    }\n  }\n  .btn-light {\n    background-color: lighten($dark, 7.5%);\n    color: $white;\n    border-color: $gray-600;\n\n    &:hover,\n    &:focus {\n      background-color: lighten($dark, 10%);\n      color: $gray-300;\n      border-color: lighten($gray-600, 5%);\n    }\n  }\n\n  @each $color, $value in $theme-colors-alt {\n    .btn-#{$color} {\n      @if $color == dark {\n        @include button-variant(darken($value, 5%), lighten($value, 10%));\n      } @else {\n        @include button-variant($value, $value);\n      }\n    }\n  }\n\n  @each $color, $value in $theme-colors-alt {\n    .btn-outline-#{$color} {\n      @if $color == dark {\n        @include button-outline-variant(darken($value, 20%));\n      } @else {\n        @include button-outline-variant($value);\n      }\n    }\n  }\n}\n","//\n// Component: Callout\n//\n\n// Base styles (regardless of theme)\n.callout {\n  @if $enable-rounded {\n    @include border-radius($border-radius);\n  }\n\n  @if $enable-shadows {\n    box-shadow: map-get($elevations, 1);\n  } @else {\n    border: 1px solid $gray-300;\n  }\n\n  background-color: $white;\n  border-left: 5px solid $gray-200;\n  margin-bottom: map-get($spacers, 3);\n  padding: 1rem;\n\n  a {\n    color: $gray-700;\n    text-decoration: underline;\n\n    &:hover {\n      color: $gray-200;\n    }\n  }\n\n  p:last-child {\n    margin-bottom: 0;\n  }\n\n  // Themes for different contexts\n  &.callout-danger {\n    border-left-color: darken(theme-color(\"danger\"), 10%);\n  }\n\n  &.callout-warning {\n    border-left-color: darken(theme-color(\"warning\"), 10%);\n  }\n\n  &.callout-info {\n    border-left-color: darken(theme-color(\"info\"), 10%);\n  }\n\n  &.callout-success {\n    border-left-color: darken(theme-color(\"success\"), 10%);\n  }\n}\n\n@include dark-mode () {\n  .callout {\n    background-color: lighten($dark, 5%);\n\n    &.callout-danger {\n      border-left-color: lighten($danger-alt, 10%);\n    }\n\n    &.callout-warning {\n      border-left-color: lighten($warning-alt, 10%);\n    }\n\n    &.callout-info {\n      border-left-color: lighten($info-alt, 10%);\n    }\n\n    &.callout-success {\n      border-left-color: lighten($success-alt, 10%);\n    }\n  }\n}\n","//\n// Component: Alert\n//\n\n.alert {\n  .icon {\n    margin-right: 10px;\n  }\n\n  .close {\n    color: $black;\n    opacity: .2;\n\n    &:hover {\n      opacity: .5;\n    }\n  }\n\n  a {\n    color: $white;\n    text-decoration: underline;\n  }\n}\n\n//Alert Variants\n@each $color, $value in $theme-colors {\n  .alert-#{$color} {\n    color: color-yiq($value);\n    background-color: $value;\n    border-color: darken($value, 5%);\n  }\n\n  .alert-default-#{$color} {\n    @include alert-variant(theme-color-level($color, $alert-bg-level), theme-color-level($color, $alert-border-level), theme-color-level($color, $alert-color-level));\n  }\n}\n\n@include dark-mode () {\n  @each $color, $value in $theme-colors-alt {\n    .alert-#{$color} {\n      color: color-yiq($value);\n      background-color: $value;\n      border-color: darken($value, 5%);\n    }\n\n    .alert-default-#{$color} {\n      @include alert-variant(theme-color-level($color, $alert-bg-level), theme-color-level($color, $alert-border-level), theme-color-level($color, $alert-color-level));\n    }\n  }\n}\n\n//\n","//\n// Component: Table\n//\n\n.table {\n  &:not(.table-dark) {\n    color: inherit;\n  }\n\n  // fixed table head\n  &.table-head-fixed {\n    thead tr:nth-child(1) th {\n      background-color: $white;\n      border-bottom: 0;\n      box-shadow: inset 0 1px 0 $table-border-color, inset 0 -1px 0 $table-border-color;\n      position: sticky;\n      top: 0;\n      z-index: 10;\n    }\n\n    &.table-dark {\n      thead tr {\n        &:nth-child(1) th {\n          background-color: $table-dark-bg;\n          box-shadow: inset 0 1px 0 $table-dark-border-color, inset 0 -1px 0 $table-dark-border-color;\n        }\n      }\n    }\n  }\n\n  // no border\n  &.no-border {\n    &,\n    td,\n    th {\n      border: 0;\n    }\n  }\n\n  // .text-center in tables\n  &.text-center {\n    &,\n    td,\n    th {\n      text-align: center;\n    }\n  }\n\n  &.table-valign-middle {\n    thead > tr > th,\n    thead > tr > td,\n    tbody > tr > th,\n    tbody > tr > td {\n      vertical-align: middle;\n    }\n  }\n\n  .card-body.p-0 & {\n    thead > tr > th,\n    thead > tr > td,\n    tfoot > tr > th,\n    tfoot > tr > td,\n    tbody > tr > th,\n    tbody > tr > td {\n      &:first-of-type {\n        padding-left: map-get($spacers, 4);\n      }\n\n      &:last-of-type {\n        padding-right: map-get($spacers, 4);\n      }\n    }\n  }\n}\n\n// Expandable Table\n\n.table-hover tbody tr.expandable-body:hover {\n  background-color: inherit !important;\n}\n\n[data-widget=\"expandable-table\"] {\n  cursor: pointer;\n\n  i.expandable-table-caret {\n    transition: transform $transition-speed linear;\n  }\n  &[aria-expanded=\"true\"] {\n    i.expandable-table-caret {\n      // stylelint-disable selector-max-attribute\n      &[class*=\"right\"] {\n        transform: rotate(90deg);\n      }\n      &[class*=\"left\"] {\n        transform: rotate(-90deg);\n      }\n      // stylelint-enable selector-max-attribute\n    }\n  }\n}\n\n[aria-expanded=\"true\"] {\n  cursor: pointer;\n\n  i.expandable-table-caret {\n    transition: transform $transition-speed linear;\n  }\n  [data-widget=\"expandable-table\"] {\n    i.expandable-table-caret {\n      // stylelint-disable selector-max-attribute\n      &[class*=\"right\"] {\n        transform: rotate(90deg);\n      }\n      &[class*=\"left\"] {\n        transform: rotate(-90deg);\n      }\n      // stylelint-enable selector-max-attribute\n    }\n  }\n}\n\n.expandable-body {\n  > td {\n    padding: 0 !important;\n    width: 100%;\n\n    > div,\n    > p {\n      padding: $table-cell-padding;\n    }\n  }\n\n  .table {\n    width: calc(100% - #{$table-cell-padding});\n    margin: 0 0 0 $table-cell-padding;\n\n    tr:first-child {\n      td,\n      th {\n        border-top: none;\n      }\n    }\n  }\n}\n\n@include dark-mode () {\n  .table-bordered {\n    &,\n    td,\n    th {\n      border-color: $gray-600;\n    }\n  }\n  .table-hover {\n    tbody tr:hover {\n      color: $gray-300;\n      background-color: lighten($dark, 2.5%);\n      border-color: $gray-600;\n    }\n  }\n  .table {\n    thead th {\n      border-bottom-color: $gray-600;\n    }\n    th,\n    td {\n      border-top-color: $gray-600;\n    }\n    &.table-head-fixed {\n      thead tr:nth-child(1) th {\n        background-color: lighten($dark, 5%);\n      }\n    }\n  }\n}\n","//\n// Component: Carousel\n//\n\n.carousel-control-custom-icon {\n  .carousel-control-prev & {\n    margin-left: -20px;\n  }\n  .carousel-control-next & {\n    margin-right: 20px;\n  }\n\n  > .fa,\n  > .fas,\n  > .far,\n  > .fab,\n  > .fal,\n  > .fad,\n  > .svg-inline--fa,\n  > .ion {\n    display: inline-block;\n    font-size: 40px;\n    margin-top: -20px;\n    position: absolute;\n    top: 50%;\n    z-index: 5;\n  }\n}\n",".close {\n  float: right;\n  @include font-size($close-font-size);\n  font-weight: $close-font-weight;\n  line-height: 1;\n  color: $close-color;\n  text-shadow: $close-text-shadow;\n  opacity: .5;\n\n  // Override <a>'s hover style\n  @include hover() {\n    color: $close-color;\n    text-decoration: none;\n  }\n\n  &:not(:disabled):not(.disabled) {\n    @include hover-focus() {\n      opacity: .75;\n    }\n  }\n\n  &:focus {\n    outline: none;\n  }\n}\n\n// Additional properties for button version\n// iOS requires the button element instead of an anchor tag.\n// If you want the anchor version, it requires `href=\"#\"`.\n// See https://developer.mozilla.org/en-US/docs/Web/Events/click#Safari_Mobile\n\n// stylelint-disable-next-line selector-no-qualifying-type\nbutton.close {\n  padding: 0;\n  background-color: transparent;\n  border: 0;\n}\n\n// Future-proof disabling of clicks on `<a>` elements\n\n// stylelint-disable-next-line selector-no-qualifying-type\na.close.disabled {\n  pointer-events: none;\n}\n","//\n// Component: Small Box\n//\n\n.small-box {\n  @include border-radius($border-radius);\n  @include box-shadow($card-shadow);\n\n  display: block;\n  margin-bottom: 20px;\n  position: relative;\n\n  // content wrapper\n  > .inner {\n    padding: 10px;\n  }\n\n  > .small-box-footer {\n    background-color: rgba($black, .1);\n    color: rgba($white, .8);\n    display: block;\n    padding: 3px 0;\n    position: relative;\n    text-align: center;\n    text-decoration: none;\n    z-index: 10;\n\n    &:hover {\n      background-color: rgba($black, .15);\n      color: $white;\n    }\n  }\n\n  h3 {\n    @include font-size(2.2rem);\n    font-weight: 700;\n    margin: 0 0 10px;\n    padding: 0;\n    white-space: nowrap;\n  }\n\n  @include media-breakpoint-up(lg) {\n    .col-xl-2 &,\n    .col-lg-2 &,\n    .col-md-2 & {\n      h3 {\n        @include font-size(1.6rem);\n      }\n    }\n\n    .col-xl-3 &,\n    .col-lg-3 &,\n    .col-md-3 & {\n      h3 {\n        @include font-size(1.6rem);\n      }\n    }\n  }\n\n  @include media-breakpoint-up(xl) {\n    .col-xl-2 &,\n    .col-lg-2 &,\n    .col-md-2 & {\n      h3 {\n        @include font-size(2.2rem);\n      }\n    }\n\n    .col-xl-3 &,\n    .col-lg-3 &,\n    .col-md-3 & {\n      h3 {\n        @include font-size(2.2rem);\n      }\n    }\n  }\n\n  p {\n    font-size: 1rem;\n\n    > small {\n      color: $gray-100;\n      display: block;\n      font-size: .9rem;\n      margin-top: 5px;\n    }\n  }\n\n  h3,\n  p {\n    z-index: 5;\n  }\n\n  // the icon\n  .icon {\n    color: rgba($black, .15);\n    z-index: 0;\n\n    > i {\n      font-size: 90px;\n      position: absolute;\n      right: 15px;\n      top: 15px;\n      transition: transform $transition-speed linear;\n\n      &.fa,\n      &.fas,\n      &.far,\n      &.fab,\n      &.fal,\n      &.fad,\n      &.ion {\n        font-size: 70px;\n        top: 20px;\n      }\n    }\n\n    svg {\n      font-size: 70px;\n      position: absolute;\n      right: 15px;\n      top: 15px;\n      transition: transform $transition-speed linear;\n    }\n  }\n\n  // Small box hover state\n  &:hover {\n    text-decoration: none;\n\n    // Animate icons on small box hover\n    .icon {\n      > i {\n        &,\n        &.fa,\n        &.fas,\n        &.far,\n        &.fab,\n        &.fal,\n        &.fad,\n        &.ion {\n          transform: scale(1.1);\n        }\n      }\n      > svg {\n        transform: scale(1.1);\n      }\n    }\n  }\n}\n\n@include media-breakpoint-down(sm) {\n  // No need for icons on very small devices\n  .small-box {\n    text-align: center;\n\n    .icon {\n      display: none;\n    }\n\n    p {\n      font-size: 12px;\n    }\n  }\n}\n","//\n// Component: Info Box\n//\n\n.info-box {\n  @include box-shadow($card-shadow);\n  @include border-radius($border-radius);\n\n  background-color: $white;\n  display: flex;\n  margin-bottom: map-get($spacers, 3);\n  min-height: 80px;\n  padding: .5rem;\n  position: relative;\n  width: 100%;\n\n  .progress {\n    background-color: rgba($black, .125);\n    height: 2px;\n    margin: 5px 0;\n\n    .progress-bar {\n      background-color: $white;\n    }\n  }\n\n  .info-box-icon {\n    @if $enable-rounded {\n      border-radius: $border-radius;\n    }\n\n    align-items: center;\n    display: flex;\n    font-size: 1.875rem;\n    justify-content: center;\n    text-align: center;\n    width: 70px;\n\n    > img {\n      max-width: 100%;\n    }\n  }\n\n  .info-box-content {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    line-height: 1.8;\n    flex: 1;\n    padding: 0 10px;\n    overflow: hidden;\n  }\n\n  .info-box-number {\n    display: block;\n    margin-top: .25rem;\n    font-weight: $font-weight-bold;\n  }\n\n  .progress-description,\n  .info-box-text {\n    display: block;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n  }\n\n  @each $name, $color in $theme-colors {\n    .info-box {\n      .bg-#{$name},\n      .bg-gradient-#{$name} {\n        color: color-yiq($color);\n\n        .progress-bar {\n          background-color: color-yiq($color);\n        }\n      }\n    }\n  }\n\n  .info-box-more {\n    display: block;\n  }\n\n  .progress-description {\n    margin: 0;\n\n  }\n\n  @include media-breakpoint-up(md) {\n    .col-xl-2 &,\n    .col-lg-2 &,\n    .col-md-2 & {\n      .progress-description {\n        display: none;\n      }\n    }\n\n    .col-xl-3 &,\n    .col-lg-3 &,\n    .col-md-3 & {\n      .progress-description {\n        display: none;\n      }\n    }\n  }\n\n  @include media-breakpoint-up(lg) {\n    .col-xl-2 &,\n    .col-lg-2 &,\n    .col-md-2 & {\n      .progress-description {\n        @include font-size(.75rem);\n        display: block;\n      }\n    }\n\n    .col-xl-3 &,\n    .col-lg-3 &,\n    .col-md-3 & {\n      .progress-description {\n        @include font-size(.75rem);\n        display: block;\n      }\n    }\n  }\n\n  @include media-breakpoint-up(xl) {\n    .col-xl-2 &,\n    .col-lg-2 &,\n    .col-md-2 & {\n      .progress-description {\n        @include font-size(1rem);\n        display: block;\n      }\n    }\n\n    .col-xl-3 &,\n    .col-lg-3 &,\n    .col-md-3 & {\n      .progress-description {\n        @include font-size(1rem);\n        display: block;\n      }\n    }\n  }\n}\n\n@include dark-mode () {\n  .info-box {\n    background-color: $dark;\n    color: $white;\n    @each $name, $color in $theme-colors-alt {\n      .info-box {\n        .bg-#{$name},\n        .bg-gradient-#{$name} {\n          color: color-yiq($color);\n\n          .progress-bar {\n            background-color: color-yiq($color);\n          }\n        }\n      }\n    }\n  }\n}\n","//\n// Component: Timeline\n//\n\n.timeline {\n  margin: 0 0 45px;\n  padding: 0;\n  position: relative;\n  // The line\n  &::before {\n    @include border-radius($border-radius);\n    background-color: $gray-300;\n    bottom: 0;\n    content: \"\";\n    left: 31px;\n    margin: 0;\n    position: absolute;\n    top: 0;\n    width: 4px;\n  }\n  // Element\n  > div {\n    &::before,\n    &::after {\n      content: \"\";\n      display: table;\n    }\n\n    margin-bottom: 15px;\n    margin-right: 10px;\n    position: relative;\n    // The content\n    > .timeline-item {\n      @include box-shadow($card-shadow);\n      @include border-radius($border-radius);\n      background-color: $white;\n      color: $gray-700;\n      margin-left: 60px;\n      margin-right: 15px;\n      margin-top: 0;\n      padding: 0;\n      position: relative;\n      // The time and header\n      > .time {\n        color: #999;\n        float: right;\n        font-size: 12px;\n        padding: 10px;\n      }\n      // Header\n      > .timeline-header {\n        border-bottom: 1px solid $card-border-color;\n        color: $gray-700;\n        font-size: 16px;\n        line-height: 1.1;\n        margin: 0;\n        padding: 10px;\n        // Link in header\n        > a {\n          font-weight: 600;\n        }\n      }\n      // Item body and footer\n      > .timeline-body,\n      > .timeline-footer {\n        padding: 10px;\n      }\n\n      > .timeline-body {\n        > img {\n          margin: 10px;\n        }\n        > dl,\n        ol,\n        ul {\n          margin: 0;\n        }\n      }\n\n      > .timeline-footer {\n        > a {\n          color: $white;\n        }\n      }\n    }\n    // The icons at line\n    > .fa,\n    > .fas,\n    > .far,\n    > .fab,\n    > .fal,\n    > .fad,\n    > .svg-inline--fa,\n    > .ion {\n      background-color: $gray-500;\n      border-radius: 50%;\n      font-size: 16px;\n      height: 30px;\n      left: 18px;\n      line-height: 30px;\n      position: absolute;\n      text-align: center;\n      top: 0;\n      width: 30px;\n    }\n    > .svg-inline--fa {\n      padding: 7px;\n    }\n  }\n  // Time label\n  > .time-label {\n    > span {\n      @include border-radius(4px);\n      background-color: $white;\n      display: inline-block;\n      font-weight: 600;\n      padding: 5px;\n    }\n  }\n}\n\n.timeline-inverse {\n  > div {\n    > .timeline-item {\n      @include box-shadow(none);\n      background-color: $gray-100;\n      border: 1px solid $gray-300;\n\n      > .timeline-header {\n        border-bottom-color: $gray-300;\n      }\n    }\n  }\n}\n\n@include dark-mode () {\n  .timeline {\n    &::before {\n      background-color: $gray-600;\n    }\n    > div > .timeline-item {\n      background-color: $dark;\n      color: $white;\n      border-color: $gray-600;\n\n      > .timeline-header {\n        color: $gray-400;\n        border-color: $gray-600;\n      }\n      > .time {\n        color: $gray-400;\n      }\n    }\n  }\n}\n","//\n// Component: Products\n//\n\n.products-list {\n  list-style: none;\n  margin: 0;\n  padding: 0;\n\n  > .item {\n    @include clearfix ();\n\n    @if $enable-rounded {\n      @include border-radius($border-radius);\n    }\n\n    background-color: $white;\n    padding: 10px 0;\n\n  }\n\n  .product-img {\n    float: left;\n\n    img {\n      height: 50px;\n      width: 50px;\n    }\n  }\n\n  .product-info {\n    margin-left: 60px;\n  }\n\n  .product-title {\n    font-weight: 600;\n  }\n\n  .product-description {\n    color: $gray-600;\n    display: block;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n  }\n}\n\n.product-list-in-card > .item {\n  @include border-radius(0);\n  border-bottom: 1px solid $card-border-color;\n\n  &:last-of-type {\n    border-bottom-width: 0;\n  }\n}\n\n\n@include dark-mode () {\n  .products-list > .item {\n    background-color: $dark;\n    color: $white;\n    border-bottom-color: $gray-600;\n  }\n\n  .product-description {\n    color: $gray-400;\n  }\n}\n","//\n// Component: Direct Chat\n//\n\n.direct-chat {\n  .card-body {\n    overflow-x: hidden;\n    padding: 0;\n    position: relative;\n  }\n\n  &.chat-pane-open {\n    .direct-chat-contacts {\n      @include translate(0, 0);\n    }\n  }\n\n\n  &.timestamp-light {\n    .direct-chat-timestamp {\n      color: lighten(color-yiq($yiq-text-light), 10%);\n    }\n  }\n\n  &.timestamp-dark {\n    .direct-chat-timestamp {\n      color: darken(color-yiq($yiq-text-dark), 20%);\n    }\n  }\n}\n\n.direct-chat-messages {\n  @include translate(0, 0);\n  height: 250px;\n  overflow: auto;\n  padding: 10px;\n}\n\n.direct-chat-msg,\n.direct-chat-text {\n  display: block;\n}\n\n.direct-chat-msg {\n  @include clearfix ();\n  margin-bottom: 10px;\n}\n\n.direct-chat-messages,\n.direct-chat-contacts {\n  transition: transform .5s ease-in-out;\n}\n\n.direct-chat-text {\n  @if $enable-rounded {\n    @include border-radius($border-radius-lg);\n  }\n\n  background-color: $direct-chat-default-msg-bg;\n  border: 1px solid $direct-chat-default-msg-border-color;\n  color: $direct-chat-default-font-color;\n  margin: 5px 0 0 50px;\n  padding: 5px 10px;\n  position: relative;\n\n  //Create the arrow\n  &::after,\n  &::before {\n    border: solid transparent;\n    border-right-color: $direct-chat-default-msg-border-color;\n    content: \" \";\n    height: 0;\n    pointer-events: none;\n    position: absolute;\n    right: 100%;\n    top: 15px;\n    width: 0;\n  }\n\n  &::after {\n    border-width: 5px;\n    margin-top: -5px;\n  }\n\n  &::before {\n    border-width: 6px;\n    margin-top: -6px;\n  }\n\n  .right & {\n    margin-left: 0;\n    margin-right: 50px;\n\n    &::after,\n    &::before {\n      border-left-color: $direct-chat-default-msg-border-color;\n      border-right-color: transparent;\n      left: 100%;\n      right: auto;\n    }\n  }\n}\n\n.direct-chat-img {\n  @include border-radius(50%);\n  float: left;\n  height: 40px;\n  width: 40px;\n\n  .right & {\n    float: right;\n  }\n}\n\n.direct-chat-infos {\n  display: block;\n  font-size: $font-size-sm;\n  margin-bottom: 2px;\n}\n\n.direct-chat-name {\n  font-weight: 600;\n}\n\n.direct-chat-timestamp {\n  color: darken($gray-500, 25%);\n}\n\n//Direct chat contacts pane\n.direct-chat-contacts-open {\n  .direct-chat-contacts {\n    @include translate(0, 0);\n  }\n}\n\n.direct-chat-contacts {\n  @include translate(101%, 0);\n  background-color: $dark;\n  bottom: 0;\n  color: $white;\n  height: 250px;\n  overflow: auto;\n  position: absolute;\n  top: 0;\n  width: 100%;\n}\n\n.direct-chat-contacts-light {\n  background-color: $light;\n\n  .contacts-list-name {\n    color: $gray-700;\n  }\n\n  .contacts-list-date {\n    color: $gray-600;\n  }\n\n  .contacts-list-msg {\n    color: darken($gray-600, 10%);\n  }\n}\n\n//Contacts list -- for displaying contacts in direct chat contacts pane\n.contacts-list {\n  @include list-unstyled ();\n\n  > li {\n    @include clearfix ();\n    border-bottom: 1px solid rgba($black, .2);\n    margin: 0;\n    padding: 10px;\n\n    &:last-of-type {\n      border-bottom: 0;\n    }\n  }\n}\n\n.contacts-list-img {\n  @include border-radius(50%);\n  float: left;\n  width: 40px;\n}\n\n.contacts-list-info {\n  color: $white;\n  margin-left: 45px;\n}\n\n.contacts-list-name,\n.contacts-list-status {\n  display: block;\n}\n\n.contacts-list-name {\n  font-weight: 600;\n}\n\n.contacts-list-status {\n  font-size: $font-size-sm;\n}\n\n.contacts-list-date {\n  color: $gray-400;\n  font-weight: 400;\n}\n\n.contacts-list-msg {\n  color: darken($gray-400, 10%);\n}\n\n// Color variants\n@each $name, $color in $theme-colors {\n  .direct-chat-#{$name} {\n    @include direct-chat-variant($color);\n  }\n}\n\n@each $name, $color in $colors {\n  .direct-chat-#{$name} {\n    @include direct-chat-variant($color);\n  }\n}\n\n@include dark-mode () {\n  .direct-chat-text {\n    background-color: lighten($dark, 7.5%);\n    border-color: lighten($dark, 10%);\n    color: $white;\n\n    &::after,\n    &::before {\n      border-right-color: lighten($dark, 10%);\n    }\n  }\n  .direct-chat-timestamp {\n    color: $gray-500;\n  }\n  .right > .direct-chat-text {\n    &::after,\n    &::before {\n      border-right-color: transparent;\n    }\n  }\n\n  // Color variants\n  @each $name, $color in $theme-colors-alt {\n    .direct-chat-#{$name} {\n      @include direct-chat-variant($color);\n    }\n  }\n\n  @each $name, $color in $colors-alt {\n    .direct-chat-#{$name} {\n      @include direct-chat-variant($color);\n    }\n  }\n}\n","//\n// Mixins: Direct Chat\n//\n\n// Direct Chat Variant\n@mixin direct-chat-variant($bg-color, $color: $white) {\n  .right > .direct-chat-text {\n    background-color: $bg-color;\n    border-color: $bg-color;\n    color: color-yiq($bg-color);\n\n    &::after,\n    &::before {\n      border-left-color: $bg-color;\n    }\n  }\n}\n","//\n// Component: Users List\n//\n\n.users-list {\n  @include list-unstyled ();\n\n  > li {\n    float: left;\n    padding: 10px;\n    text-align: center;\n    width: 25%;\n\n    img {\n      @include border-radius(50%);\n      height: auto;\n      max-width: 100%;\n    }\n\n    > a:hover {\n      &,\n      .users-list-name {\n        color: #999;\n      }\n    }\n  }\n}\n\n.users-list-name,\n.users-list-date {\n  display: block;\n}\n\n.users-list-name {\n  color: $gray-700;\n  font-size: $font-size-sm;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n\n.users-list-date {\n  color: darken($gray-500, 20%);\n  font-size: 12px;\n}\n\n@include dark-mode () {\n  .users-list-name {\n    color: $gray-400;\n  }\n  .users-list-date {\n    color: $gray-500;\n  }\n}\n","//\n// Component: Social Widgets\n//\n\n//General widget style\n.card-widget {\n  border: 0;\n  position: relative;\n}\n\n//User Widget Style 1\n.widget-user {\n\n  //User name container\n  .widget-user-header {\n    @if $enable-rounded {\n      @include border-top-radius($border-radius);\n    }\n\n    height: 135px;\n    padding: 1rem;\n    text-align: center;\n  }\n\n  //User name\n  .widget-user-username {\n    font-size: 25px;\n    font-weight: 300;\n    margin-bottom: 0;\n    margin-top: 0;\n    text-shadow: 0 1px 1px rgba($black, .2);\n  }\n\n  //User single line description\n  .widget-user-desc {\n    margin-top: 0;\n  }\n\n  //User image container\n  .widget-user-image {\n    left: 50%;\n    margin-left: -45px;\n    position: absolute;\n    top: 80px;\n\n    > img {\n      border: 3px solid $white;\n      height: auto;\n      width: 90px;\n    }\n  }\n\n  .card-footer {\n    padding-top: 50px;\n  }\n}\n\n//User Widget Style 2\n.widget-user-2 {\n\n  //User name container\n  .widget-user-header {\n    @include border-top-radius($border-radius);\n    padding: 1rem;\n  }\n\n  //User name\n  .widget-user-username {\n    font-size: 25px;\n    font-weight: 300;\n    margin-bottom: 5px;\n    margin-top: 5px;\n  }\n\n  //User single line description\n  .widget-user-desc {\n    margin-top: 0;\n  }\n\n  .widget-user-username,\n  .widget-user-desc {\n    margin-left: 75px;\n  }\n\n  //User image container\n  .widget-user-image {\n    > img {\n      float: left;\n      height: auto;\n      width: 65px;\n    }\n  }\n}\n","//\n// Pages: Mailbox\n//\n\n.mailbox-messages {\n  > .table {\n    margin: 0;\n  }\n}\n\n.mailbox-controls {\n  padding: 5px;\n\n  &.with-border {\n    border-bottom: 1px solid $card-border-color;\n  }\n}\n\n.mailbox-read-info {\n  border-bottom: 1px solid $card-border-color;\n  padding: 10px;\n\n  h3 {\n    font-size: 20px;\n    margin: 0;\n  }\n\n  h5 {\n    margin: 0;\n    padding: 5px 0 0;\n  }\n}\n\n.mailbox-read-time {\n  color: #999;\n  font-size: 13px;\n}\n\n.mailbox-read-message {\n  padding: 10px;\n}\n\n.mailbox-attachments {\n  @include list-unstyled ();\n  li {\n    border: 1px solid #eee;\n    float: left;\n    margin-bottom: 10px;\n    margin-right: 10px;\n    width: 200px;\n  }\n}\n\n.mailbox-attachment-name {\n  color: #666;\n  font-weight: 700;\n}\n\n.mailbox-attachment-icon,\n.mailbox-attachment-info,\n.mailbox-attachment-size {\n  display: block;\n}\n\n.mailbox-attachment-info {\n  background-color: $gray-100;\n  padding: 10px;\n}\n\n.mailbox-attachment-size {\n  color: #999;\n  font-size: 12px;\n\n  > span {\n    display: inline-block;\n    padding-top: .75rem;\n  }\n}\n\n.mailbox-attachment-icon {\n  color: #666;\n  font-size: 65px;\n  max-height: 132.5px;\n  padding: 20px 10px;\n  text-align: center;\n\n  &.has-img {\n    padding: 0;\n\n    > img {\n      height: auto;\n      max-width: 100%;\n    }\n  }\n}\n\n.mailbox-attachment-close {\n  @extend .close;\n}\n","//\n// Pages: Lock Screen\n//\n\n// ADD THIS CLASS TO THE <BODY> TAG\n.lockscreen {\n  background-color: $gray-200;\n\n  // User name [optional]\n  .lockscreen-name {\n    font-weight: 600;\n    text-align: center;\n  }\n}\n\n.lockscreen-logo {\n  font-size: 35px;\n  font-weight: 300;\n  margin-bottom: 25px;\n  text-align: center;\n\n  a {\n    color: $gray-700;\n  }\n}\n\n.lockscreen-wrapper {\n  margin: 0 auto;\n  margin-top: 10%;\n  max-width: 400px;\n}\n\n\n// Will contain the image and the sign in form\n.lockscreen-item {\n  @include border-radius(4px);\n  background-color: $white;\n  margin: 10px auto 30px;\n  padding: 0;\n  position: relative;\n  width: 290px;\n}\n\n// User image\n.lockscreen-image {\n  @include border-radius(50%);\n  background-color: $white;\n  left: -10px;\n  padding: 5px;\n  position: absolute;\n  top: -25px;\n  z-index: 10;\n\n  > img {\n    @include border-radius(50%);\n    height: 70px;\n    width: 70px;\n  }\n}\n\n// Contains the password input and the login button\n.lockscreen-credentials {\n  margin-left: 70px;\n\n  .form-control {\n    border: 0;\n  }\n\n  .btn {\n    background-color: $white;\n    border: 0;\n    padding: 0 10px;\n  }\n}\n\n.lockscreen-footer {\n  margin-top: 10px;\n}\n\n@include dark-mode () {\n  .lockscreen-item {\n    background-color: $dark;\n  }\n  .lockscreen-logo a {\n    color: $white;\n  }\n  .lockscreen-credentials .btn {\n    background-color: $dark;\n  }\n  .lockscreen-image {\n    background-color: $gray-600;\n  }\n}\n","//\n// Pages: Login & Register\n//\n\n.login-logo,\n.register-logo {\n  font-size: 2.1rem;\n  font-weight: 300;\n  margin-bottom: .9rem;\n  text-align: center;\n\n  a {\n    color: $gray-700;\n  }\n}\n\n.login-page,\n.register-page {\n  align-items: center;\n  background-color: $gray-200;\n  display: flex;\n  flex-direction: column;\n  height: 100vh;\n  justify-content: center;\n}\n\n.login-box,\n.register-box {\n  width: 360px;\n\n  @media (max-width: map-get($grid-breakpoints, sm)) {\n    margin-top: .5rem;\n    width: 90%;\n  }\n\n  .card {\n    margin-bottom: 0;\n  }\n}\n\n.login-card-body,\n.register-card-body {\n  background-color: $white;\n  border-top: 0;\n  color: #666;\n  padding: 20px;\n\n  .input-group {\n    .form-control {\n      border-right: 0;\n\n      &:focus {\n        box-shadow: none;\n\n        ~ .input-group-prepend .input-group-text,\n        ~ .input-group-append .input-group-text {\n          border-color: $input-focus-border-color;\n        }\n      }\n\n      &.is-valid {\n        &:focus {\n          box-shadow: none;\n        }\n\n        ~ .input-group-prepend .input-group-text,\n        ~ .input-group-append .input-group-text {\n          border-color: $success;\n        }\n      }\n\n      &.is-invalid {\n        &:focus {\n          box-shadow: none;\n        }\n\n        ~ .input-group-append .input-group-text {\n          border-color: $danger;\n        }\n      }\n    }\n\n    .input-group-text {\n      background-color: transparent;\n      border-bottom-right-radius: $border-radius;\n      border-left: 0;\n      border-top-right-radius: $border-radius;\n      color: #777;\n      transition: $input-transition;\n    }\n  }\n}\n\n.login-box-msg,\n.register-box-msg {\n  margin: 0;\n  padding: 0 20px 20px;\n  text-align: center;\n}\n\n.social-auth-links {\n  margin: 10px 0;\n}\n\n@include dark-mode () {\n  .login-card-body,\n  .register-card-body {\n    background-color: $dark;\n    border-color: $gray-600;\n    color: $white;\n  }\n  .login-logo,\n  .register-logo {\n    a {\n      color: $white;\n    }\n  }\n}\n","//\n// Pages: 400 and 500 error pages\n//\n\n.error-page {\n  margin: 20px auto 0;\n  width: 600px;\n\n  @include media-breakpoint-down(sm) {\n    width: 100%;\n  }\n\n  //For the error number e.g: 404\n  > .headline {\n    float: left;\n    font-size: 100px;\n    font-weight: 300;\n\n    @include media-breakpoint-down(sm) {\n      float: none;\n      text-align: center;\n    }\n  }\n\n  //For the message\n  > .error-content {\n    display: block;\n    margin-left: 190px;\n\n    @include media-breakpoint-down(sm) {\n      margin-left: 0;\n    }\n\n    > h3 {\n      font-size: 25px;\n      font-weight: 300;\n\n      @include media-breakpoint-down(sm) {\n        text-align: center;\n      }\n    }\n  }\n}\n","//\n// Pages: Invoice\n//\n\n.invoice {\n  background-color: $white;\n  border: 1px solid $card-border-color;\n  position: relative;\n}\n\n.invoice-title {\n  margin-top: 0;\n}\n\n@include dark-mode () {\n  .invoice {\n    background-color: $dark;\n  }\n}\n","//\n// Pages: Profile\n//\n\n.profile-user-img {\n  border: 3px solid $gray-500;\n  margin: 0 auto;\n  padding: 3px;\n  width: 100px;\n}\n\n.profile-username {\n  font-size: 21px;\n  margin-top: 5px;\n}\n\n.post {\n  border-bottom: 1px solid $gray-500;\n  color: #666;\n  margin-bottom: 15px;\n  padding-bottom: 15px;\n\n  &:last-of-type {\n    border-bottom: 0;\n    margin-bottom: 0;\n    padding-bottom: 0;\n  }\n\n  .user-block {\n    margin-bottom: 15px;\n    width: 100%;\n  }\n\n  .row {\n    width: 100%;\n  }\n}\n\n@include dark-mode () {\n  .post {\n    color: $white;\n    border-color: $gray-600;\n  }\n}\n","//\n// Pages: E-commerce\n//\n\n// product image\n.product-image {\n  @include img-fluid ();\n  width: 100%;\n}\n\n// product image thumbnails list\n.product-image-thumbs {\n  align-items: stretch;\n  display: flex;\n  margin-top: 2rem;\n}\n\n// product image thumbnail\n.product-image-thumb {\n  @include box-shadow($thumbnail-box-shadow);\n  @include border-radius($thumbnail-border-radius);\n\n  background-color: $thumbnail-bg;\n  border: $thumbnail-border-width solid $thumbnail-border-color;\n  display: flex;\n  margin-right: 1rem;\n  max-width: 6.5rem + ($thumbnail-padding * 2);\n  padding: $thumbnail-padding * 2;\n\n  img {\n    @include img-fluid ();\n    align-self: center;\n  }\n\n  &:hover {\n    opacity: .5;\n  }\n}\n\n// product share\n.product-share {\n  a {\n    margin-right: .5rem;\n  }\n}\n","//\n// Pages: Projects\n//\n\n.projects {\n  td {\n    vertical-align: middle;\n  }\n\n  .list-inline {\n    margin-bottom: 0;\n  }\n\n  // table avatar\n  img.table-avatar,\n  .table-avatar img {\n    border-radius: 50%;\n    display: inline;\n    width: 2.5rem;\n  }\n\n  // project state\n  .project-state {\n    text-align: center;\n  }\n}\n","body.iframe-mode {\n  .main-sidebar {\n    display: none;\n  }\n  .content-wrapper {\n    margin-left: 0 !important;\n    margin-top: 0 !important;\n    padding-bottom: 0 !important;\n  }\n  .main-header,\n  .main-footer {\n    display: none;\n  }\n}\n\nbody.iframe-mode-fullscreen {\n  overflow: hidden;\n\n  &.layout-navbar-fixed .wrapper .content-wrapper {\n    margin-top: 0 !important;\n  }\n}\n\n.content-wrapper {\n  height: 100%;\n\n  &.iframe-mode {\n    .btn-iframe-close {\n      color: $danger;\n      position: absolute;\n      line-height: 1;\n      right: .125rem;\n      top: .125rem;\n      z-index: 10;\n      visibility: hidden;\n\n      &:hover,\n      &:focus {\n        animation-name: fadeIn;\n        animation-duration: $transition-speed;\n        animation-fill-mode: both;\n        visibility: visible;\n      }\n\n      @include on-touch-device() {\n        visibility: visible;\n      }\n    }\n    .navbar-nav {\n      overflow-y: auto;\n      width: 100%;\n\n      .nav-link {\n        white-space: nowrap;\n      }\n      .nav-item {\n        position: relative;\n\n        &:hover,\n        &:focus {\n          .btn-iframe-close {\n            animation-name: fadeIn;\n            animation-duration: $transition-speed;\n            animation-fill-mode: both;\n            visibility: visible;\n\n            @include on-touch-device() {\n              visibility: visible;\n            }\n          }\n        }\n      }\n    }\n    .tab-content {\n      position: relative;\n    }\n    .tab-pane + .tab-empty {\n      display: none;\n    }\n    .tab-empty {\n      width: 100%;\n      display: flex;\n      justify-content: center;\n      align-items: center;\n    }\n    .tab-loading {\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 100%;\n      display: none;\n      background-color: $main-bg;\n\n      > div {\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        width: 100%;\n        height: 100%;\n      }\n    }\n\n    iframe {\n      border: 0;\n      width: 100%;\n      height: 100%;\n      margin-bottom: -8px;\n\n      .content-wrapper {\n        padding-bottom: 0 !important;\n      }\n    }\n\n    body.iframe-mode-fullscreen & {\n      position: absolute;\n      left: 0;\n      top: 0;\n      right: 0;\n      bottom: 0;\n      margin-left: 0 !important;\n      height: 100%;\n      min-height: 100%;\n      z-index: $zindex-main-sidebar + 10;\n    }\n  }\n}\n\n.permanent-btn-iframe-close {\n  .btn-iframe-close {\n    animation: none !important;\n    visibility: visible !important;\n    opacity: 1;\n  }\n}\n\n@include dark-mode () {\n  .content-wrapper.iframe-mode {\n    .tab-loading {\n      background-color: $dark;\n    }\n  }\n}\n","//\n// Mixins: Touch Support\n//\n\n@mixin on-touch-device {\n  @media (hover: none) and (pointer: coarse) {\n    @content;\n  }\n}\n\n//\n",".content-wrapper.kanban {\n  height: 1px;\n\n  .content {\n    height: 100%;\n    overflow-x: auto;\n    overflow-y: hidden;\n\n    .container,\n    .container-fluid {\n      width: max-content;\n      display: flex;\n      align-items: stretch;\n    }\n  }\n  .content-header + .content {\n    height: calc(100% - ((2 * 15px) + (1.8rem * #{$headings-line-height})));\n  }\n\n  .card {\n    .card-body {\n      padding: .5rem;\n    }\n\n    &.card-row {\n      width: 340px;\n      display: inline-block;\n      margin: 0 .5rem;\n\n      &:first-child {\n        margin-left: 0;\n      }\n\n      .card-body {\n        height: calc(100% - (12px + (1.8rem * #{$headings-line-height}) + .5rem));\n        overflow-y: auto;\n      }\n\n      .card {\n        &:last-child {\n          margin-bottom: 0;\n          border-bottom-width: 1px;\n        }\n        .card-header {\n          padding: .5rem .75rem;\n        }\n        .card-body {\n          padding: .75rem;\n        }\n      }\n    }\n  }\n  .btn-tool {\n    &.btn-link {\n      text-decoration: underline;\n      padding-left: 0;\n      padding-right: 0;\n    }\n  }\n}\n","//\n// Plugin: Full Calendar\n//\n\n// Buttons\n.fc-button {\n  background: $gray-100;\n  background-image: none;\n  border-bottom-color: #ddd;\n  border-color: #ddd;\n  color: $gray-700;\n\n  &:hover,\n  &:active,\n  &.hover {\n    background-color: #e9e9e9;\n  }\n}\n\n// Calendar title\n.fc-header-title h2 {\n  color: #666;\n  font-size: 15px;\n  line-height: 1.6em;\n  margin-left: 10px;\n}\n\n.fc-header-right {\n  padding-right: 10px;\n}\n\n.fc-header-left {\n  padding-left: 10px;\n}\n\n// Calendar table header cells\n.fc-widget-header {\n  background: #fafafa;\n}\n\n.fc-grid {\n  border: 0;\n  width: 100%;\n}\n\n.fc-widget-header:first-of-type,\n.fc-widget-content:first-of-type {\n  border-left: 0;\n  border-right: 0;\n}\n\n.fc-widget-header:last-of-type,\n.fc-widget-content:last-of-type {\n  border-right: 0;\n}\n\n.fc-toolbar,\n.fc-toolbar.fc-header-toolbar {\n  margin: 0;\n  padding: 1rem;\n}\n\n@include media-breakpoint-down(xs) {\n  .fc-toolbar {\n    flex-direction: column;\n\n    .fc-left {\n      order: 1;\n      margin-bottom: .5rem;\n    }\n\n    .fc-center {\n      order: 0;\n      margin-bottom: .375rem;\n    }\n\n    .fc-right {\n      order: 2;\n    }\n  }\n}\n\n.fc-day-number {\n  font-size: 20px;\n  font-weight: 300;\n  padding-right: 10px;\n}\n\n.fc-color-picker {\n  list-style: none;\n  margin: 0;\n  padding: 0;\n\n  > li {\n    float: left;\n    font-size: 30px;\n    line-height: 30px;\n    margin-right: 5px;\n\n    .fa,\n    .fas,\n    .far,\n    .fab,\n    .fal,\n    .fad,\n    .svg-inline--fa,\n    .ion {\n      transition: transform linear .3s;\n\n      &:hover {\n        @include rotate(30deg);\n      }\n    }\n  }\n}\n\n#add-new-event {\n  transition: all linear .3s;\n}\n\n.external-event {\n  @include box-shadow($card-shadow);\n\n  border-radius: $border-radius;\n  cursor: move;\n  font-weight: 700;\n  margin-bottom: 4px;\n  padding: 5px 10px;\n\n  &:hover {\n    @include box-shadow(inset 0 0 90px rgba(0, 0, 0, 0.2));\n  }\n}\n","//\n// Plugin: Select2\n//\n\n//Signle select\n// .select2-container--default,\n// .select2-selection {\n//   &.select2-container--focus,\n//   &:focus,\n//   &:active {\n//     outline: none;\n//   }\n// }\n\n.select2-container--default {\n  .select2-selection--single {\n    border: $input-border-width solid $input-border-color;\n    //border-radius: $input-radius;\n    padding: ($input-padding-y * 1.25) $input-padding-x;\n    height: $input-height;\n  }\n\n  &.select2-container--open {\n    .select2-selection--single {\n      border-color: lighten($primary, 25%);\n    }\n  }\n\n  & .select2-dropdown {\n    border: $input-border-width solid $input-border-color;\n    //border-radius: $input-radius;\n  }\n\n  & .select2-results__option {\n    padding: 6px 12px;\n    user-select: none;\n  }\n\n  & .select2-selection--single .select2-selection__rendered {\n    padding-left: 0;\n    //padding-right: 0;\n    height: auto;\n    margin-top: -3px;\n  }\n\n  &[dir=\"rtl\"] .select2-selection--single .select2-selection__rendered {\n    padding-right: 6px;\n    padding-left: 20px;\n  }\n\n  & .select2-selection--single .select2-selection__arrow {\n    height: 31px;\n    right: 6px;\n  }\n\n  & .select2-selection--single .select2-selection__arrow b {\n    margin-top: 0;\n  }\n\n  .select2-dropdown,\n  .select2-search--inline {\n    .select2-search__field {\n      border: $input-border-width solid $input-border-color;\n\n      &:focus {\n        outline: none;\n        border: $input-border-width solid $input-focus-border-color;\n      }\n    }\n  }\n\n  .select2-dropdown {\n    &.select2-dropdown--below {\n      border-top: 0;\n    }\n\n    &.select2-dropdown--above {\n      border-bottom: 0;\n    }\n  }\n\n  .select2-results__option {\n    &[aria-disabled='true'] {\n      color: $gray-600;\n    }\n\n    &[aria-selected='true'] {\n      $color: $gray-300;\n\n      background-color: $color;\n\n      &,\n      &:hover {\n        color: color-yiq($color);\n      }\n    }\n  }\n\n  .select2-results__option--highlighted {\n    $color: $primary;\n    background-color: $color;\n    color: color-yiq($color);\n\n    &[aria-selected] {\n      $color: darken($color, 3%);\n\n      &,\n      &:hover {\n        background-color: $color;\n        color: color-yiq($color);\n      }\n    }\n  }\n\n  //Multiple select\n  & {\n    .select2-selection--multiple {\n      border: $input-border-width solid $input-border-color;\n      min-height: $input-height;\n\n      &:focus {\n        border-color: $input-focus-border-color;\n      }\n\n      .select2-selection__rendered {\n        padding: 0 $input-padding-x * 0.5 $input-padding-y;\n        margin-bottom: -$input-padding-x * 0.5;\n\n        li:first-child.select2-search.select2-search--inline {\n          width: 100%;\n          margin-left: $input-padding-x * 0.5;\n\n          .select2-search__field {\n            width: 100% !important;\n          }\n        }\n\n\n        .select2-search.select2-search--inline {\n          .select2-search__field {\n            border: 0;\n            margin-top: 6px;\n          }\n        }\n      }\n\n      .select2-selection__choice {\n        background-color: $primary;\n        border-color: darken($primary, 5%);\n        color: color-yiq($primary);\n        padding: 0 10px;\n        margin-top: .31rem;\n      }\n\n      .select2-selection__choice__remove {\n        color: rgba(255, 255, 255, 0.7);\n        float: right;\n        margin-left: 5px;\n        margin-right: -2px;\n\n        &:hover {\n          color: $white;\n        }\n      }\n\n      .text-sm &,\n      &.text-sm {\n        .select2-search.select2-search--inline {\n          .select2-search__field {\n            margin-top: 8px;\n          }\n        }\n\n        .select2-selection__choice {\n          margin-top: .4rem;\n        }\n      }\n    }\n\n    &.select2-container--focus {\n      .select2-selection--single,\n      .select2-selection--multiple {\n        border-color: $input-focus-border-color;\n      }\n\n      .select2-search__field {\n        border: 0;\n      }\n    }\n  }\n\n  & .select2-selection--single .select2-selection__rendered li {\n    padding-right: 10px;\n  }\n\n  .input-group-prepend ~ & {\n    .select2-selection {\n      border-bottom-left-radius: 0;\n      border-top-left-radius: 0;\n    }\n  }\n\n  .input-group > &:not(:last-child) {\n    .select2-selection {\n      border-bottom-right-radius: 0;\n      border-top-right-radius: 0;\n    }\n  }\n}\n\n// Select2 Bootstrap4 Theme overrides\n.select2-container--bootstrap4 {\n  &.select2-container--focus .select2-selection {\n    box-shadow: none;\n  }\n}\n\n// text-sm / form-control-sm override\nselect.form-control-sm ~ {\n  .select2-container--default {\n    font-size: 75%;\n  }\n}\n\n.text-sm,\nselect.form-control-sm ~ {\n  .select2-container--default {\n    .select2-selection--single {\n      height: $input-height-sm;\n\n      .select2-selection__rendered {\n        margin-top: -.4rem;\n      }\n\n      .select2-selection__arrow {\n        top: -.12rem;\n      }\n    }\n\n    .select2-selection--multiple {\n      min-height: $input-height-sm;\n\n      .select2-selection__rendered {\n        padding: 0 $input-padding-x-sm * 0.5 $input-padding-y-sm;\n        margin-top: -($input-padding-x-sm * .2);\n\n        li:first-child.select2-search.select2-search--inline {\n          margin-left: $input-padding-x-sm * 0.5;\n        }\n\n        .select2-search.select2-search--inline {\n          .select2-search__field {\n            margin-top: 6px;\n          }\n        }\n      }\n    }\n  }\n}\n\n// Dropdown Fix inside maximized card\n.maximized-card .select2-dropdown {\n  z-index: 9999;\n}\n\n// Background colors (theme colors)\n@each $name, $color in $theme-colors {\n  @include select2-variant($name, $color);\n}\n\n// Background colors (colors)\n@each $name, $color in $colors {\n  @include select2-variant($name, $color);\n}\n\n@include dark-mode () {\n  .select2-selection {\n    background-color: $dark;\n    border-color: $gray-600;\n  }\n\n  .select2-container--disabled .select2-selection--single {\n    background-color: lighten($dark, 7.5%);\n  }\n\n  .select2-selection--single {\n    background-color: $dark;\n    border-color: $gray-600;\n\n    .select2-selection__rendered {\n      color: $white;\n    }\n  }\n  .select2-dropdown .select2-search__field,\n  .select2-search--inline .select2-search__field {\n    background-color: $dark;\n    border-color: $gray-600;\n    color: white;\n  }\n  .select2-dropdown {\n    background-color: $dark;\n    border-color: $gray-600;\n    color: white;\n  }\n  .select2-results__option[aria-selected=\"true\"] {\n    background-color: lighten($dark, 5%) !important;\n    color: $gray-300;\n  }\n  .select2-container .select2-search--inline .select2-search__field {\n    background-color: transparent;\n    color: $white;\n  }\n\n  .select2-container--bootstrap4 .select2-selection--multiple .select2-selection__choice {\n    color: $white;\n  }\n\n  // Background colors (theme colors)\n  @each $name, $color in $theme-colors-alt {\n    @include select2-variant($name, $color);\n  }\n\n  // Background colors (colors)\n  @each $name, $color in $colors-alt {\n    @include select2-variant($name, $color);\n  }\n}\n","//\n// General: Mixins\n//\n\n// Select2 Variant\n@mixin select2-variant($name, $color) {\n  .select2-#{$name} {\n\n    + .select2-container--default {\n      &.select2-container--open {\n        .select2-selection--single {\n          border-color: lighten($color, 25%);\n        }\n      }\n\n      &.select2-container--focus .select2-selection--single {\n        border-color: lighten($color, 25%);\n      }\n    }\n\n    .select2-container--default &,\n    .select2-container--default {\n      &.select2-dropdown,\n      .select2-dropdown,\n      .select2-search--inline {\n        .select2-search__field {\n          &:focus {\n            border: $input-border-width solid lighten($color, 25%);\n          }\n        }\n      }\n\n      .select2-results__option--highlighted {\n        background-color: $color;\n        color: color-yiq($color);\n\n        &[aria-selected] {\n          &,\n          &:hover {\n            background-color: darken($color, 3%);\n            color: color-yiq(darken($color, 3%));\n          }\n        }\n      }\n\n      //Multiple select\n      & {\n        .select2-selection--multiple {\n          &:focus {\n            border-color: lighten($color, 25%);\n          }\n\n          .select2-selection__choice {\n            background-color: $color;\n            border-color: darken($color, 5%);\n            color: color-yiq($color);\n          }\n\n          .select2-selection__choice__remove {\n            color: rgba(color-yiq($color), 0.7);\n\n            &:hover {\n              color: color-yiq($color);\n            }\n          }\n        }\n\n        &.select2-container--focus .select2-selection--multiple {\n          border-color: lighten($color, 25%);\n        }\n      }\n    }\n  }\n}\n","//\n// Plugin: Bootstrap Slider\n//\n\n// Tooltip fix\n.slider .tooltip.in {\n  opacity: $tooltip-opacity;\n}\n\n// Style override\n.slider {\n  &.slider-vertical {\n    height: 100%;\n  }\n  &.slider-horizontal {\n    width: 100%;\n  }\n}\n\n// Colors\n@each $name, $color in $theme-colors {\n  .slider-#{$name} .slider {\n    .slider-selection {\n      background: $color;\n    }\n  }\n}\n\n@each $name, $color in $colors {\n  .slider-#{$name} .slider {\n    .slider-selection {\n      background: $color;\n    }\n  }\n}\