    color: color-yiq($color);\n      }\n    }\n  }\n\n  .bootstrap-switch-handle-on {\n    border-bottom-left-radius: $bootstrap-switch-handle-border-radius;\n    border-top-left-radius: $bootstrap-switch-handle-border-radius;\n  }\n\n  .bootstrap-switch-handle-off {\n    border-bottom-right-radius: $bootstrap-switch-handle-border-radius;\n    border-top-right-radius: $bootstrap-switch-handle-border-radius;\n  }\n\n  input[type='radio'],\n  input[type='checkbox'] {\n    filter: alpha(opacity=0);\n    left: 0;\n    margin: 0;\n    opacity: 0;\n    position: absolute;\n    top: 0;\n    visibility: hidden;\n    z-index: -1;\n  }\n\n  &.bootstrap-switch-mini {\n    .bootstrap-switch-handle-on,\n    .bootstrap-switch-handle-off,\n    .bootstrap-switch-label {\n      font-size: .875rem;\n      line-height: 1.5;\n      padding: .1rem .3rem;\n    }\n  }\n\n  &.bootstrap-switch-small {\n    .bootstrap-switch-handle-on,\n    .bootstrap-switch-handle-off,\n    .bootstrap-switch-label {\n      font-size: .875rem;\n      line-height: 1.5;\n      padding: .2rem .4rem;\n    }\n  }\n\n  &.bootstrap-switch-large {\n    .bootstrap-switch-handle-on,\n    .bootstrap-switch-handle-off,\n    .bootstrap-switch-label {\n      font-size: 1.25rem;\n      line-height: 1.3333333rem;\n      padding: .3rem .5rem;\n    }\n  }\n\n  &.bootstrap-switch-disabled,\n  &.bootstrap-switch-readonly,\n  &.bootstrap-switch-indeterminate {\n    cursor: default;\n\n    .bootstrap-switch-handle-on,\n    .bootstrap-switch-handle-off,\n    .bootstrap-switch-label {\n      cursor: default;\n      filter: alpha(opacity=50);\n      opacity: .5;\n    }\n  }\n\n  &.bootstrap-switch-animate .bootstrap-switch-container {\n    transition: margin-left .5s;\n  }\n\n  &.bootstrap-switch-inverse {\n    .bootstrap-switch-handle-on {\n      border-radius: 0 $bootstrap-switch-handle-border-radius $bootstrap-switch-handle-border-radius 0;\n    }\n\n    .bootstrap-switch-handle-off {\n      border-radius: $bootstrap-switch-handle-border-radius 0 0 $bootstrap-switch-handle-border-radius;\n    }\n  }\n\n  // &.bootstrap-switch-focused {\n  //   border-color: $input-btn-focus-color;\n  //   box-shadow: $input-btn-focus-box-shadow;\n  //   outline: 0;\n  // }\n\n  &.bootstrap-switch-on .bootstrap-switch-label,\n  &.bootstrap-switch-inverse.bootstrap-switch-off .bootstrap-switch-label {\n    border-bottom-right-radius: $bootstrap-switch-handle-border-radius;\n    border-top-right-radius: $bootstrap-switch-handle-border-radius;\n  }\n\n  &.bootstrap-switch-off .bootstrap-switch-label,\n  &.bootstrap-switch-inverse.bootstrap-switch-on .bootstrap-switch-label {\n    border-bottom-left-radius: $bootstrap-switch-handle-border-radius;\n    border-top-left-radius: $bootstrap-switch-handle-border-radius;\n  }\n}\n\n@include dark-mode () {\n  .bootstrap-switch {\n    border-color: $gray-600;\n\n    .bootstrap-switch-handle-off.bootstrap-switch-default,\n    .bootstrap-switch-handle-on.bootstrap-switch-default {\n      background-color: lighten($dark, 2.5%);\n      color: $white;\n      border-color: lighten($dark, 7.5%);\n    }\n    .bootstrap-switch-handle-on,\n    .bootstrap-switch-handle-off {\n      @each $name, $color in $theme-colors-alt {\n        &.bootstrap-switch-#{$name} {\n          background: $color;\n          color: color-yiq($color);\n        }\n      }\n\n      @each $name, $color in $colors-alt {\n        &.bootstrap-switch-#{$name} {\n          background: $color;\n          color: color-yiq($color);\n        }\n      }\n    }\n  }\n}\n","@include dark-mode() {\n  .daterangepicker {\n    background-color: lighten($dark, 5%);\n    border: inherit;\n\n    &::before,\n    &::after {\n      border-bottom-color: lighten($dark, 5%);\n    }\n\n    td.available:hover,\n    th.available:hover {\n      background-color: lighten($dark, 5%);\n    }\n    td.in-range {\n      background-color: lighten($dark, 10%);\n      color: $white;\n    }\n\n    td.off,\n    td.off.in-range,\n    td.off.start-date,\n    td.off.end-date {\n      background-color: darken($dark, 5%);\n      color: $white;\n    }\n\n    .ranges li:hover {\n      background-color: $dark;\n    }\n\n    &.show-ranges.ltr .drp-calendar {\n      border-color: lighten($dark, 10%);\n\n      &.left,\n      &.right {\n        border-color: lighten($dark, 10%);\n        padding-top: 0;\n      }\n    }\n\n    .drp-buttons {\n      border-color: lighten($dark, 10%);\n    }\n\n    .calendar-table {\n      background-color: $dark;\n      border-color: lighten($dark, 10%);\n\n      th,\n      td {\n        color: $white;\n      }\n\n      .next span,\n      .prev span {\n        border-color: $white;\n      }\n    }\n\n    select.hourselect,\n    select.minuteselect,\n    select.secondselect,\n    select.ampmselect {\n      background-color: $dark;\n      border-color: lighten($dark, 10%);\n    }\n  }\n}\n","//\n// Plugins: Miscellaneous\n// Old plugin codes\n//\n\n// _fix for sparkline tooltip\n.jqstooltip {\n  height: auto !important;\n  padding: 5px !important;\n  width: auto !important;\n}\n\n// jQueryUI\n.connectedSortable {\n  min-height: 100px;\n}\n\n.ui-helper-hidden-accessible {\n  border: 0;\n  clip: rect(0 0 0 0);\n  height: 1px;\n  margin: -1px;\n  overflow: hidden;\n  padding: 0;\n  position: absolute;\n  width: 1px;\n}\n\n.sort-highlight {\n  background: $gray-100;\n  border: 1px dashed $gray-300;\n  margin-bottom: 10px;\n}\n\n// Charts\n.chart {\n  overflow: hidden;\n  position: relative;\n}\n\n\n@include dark-mode () {\n  .irs--flat .irs-line {\n    background-color: lighten($dark, 10%);\n  }\n  .jsgrid-edit-row > .jsgrid-cell,\n  .jsgrid-filter-row > .jsgrid-cell,\n  .jsgrid-grid-body, .jsgrid-grid-header,\n  .jsgrid-header-row > .jsgrid-header-cell,\n  .jsgrid-insert-row > .jsgrid-cell,\n  .jsgrid-row > .jsgrid-cell,\n  .jsgrid-alt-row > .jsgrid-cell {\n    border-color: $gray-600;\n  }\n  .jsgrid-header-row > .jsgrid-header-cell,\n  .jsgrid-row > .jsgrid-cell {\n    background-color: $dark;\n  }\n  .jsgrid-alt-row > .jsgrid-cell {\n    background-color: lighten($dark, 2.5%);\n  }\n  .jsgrid-selected-row > .jsgrid-cell {\n    background-color: lighten($dark, 5%);\n  }\n}\n","//\n// Misc: Miscellaneous\n//\n\n.border-transparent {\n  border-color: transparent !important;\n}\n\n// Description Blocks\n.description-block {\n  display: block;\n  margin: 10px 0;\n  text-align: center;\n\n  &.margin-bottom {\n    margin-bottom: 25px;\n  }\n\n  > .description-header {\n    font-size: 16px;\n    font-weight: 600;\n    margin: 0;\n    padding: 0;\n  }\n\n  > .description-text {\n    text-transform: uppercase;\n  }\n\n  // Description Block Extension\n  .description-icon {\n    font-size: 16px;\n  }\n}\n\n// List utility classes\n.list-group-unbordered {\n  > .list-group-item {\n    border-left: 0;\n    border-radius: 0;\n    border-right: 0;\n    padding-left: 0;\n    padding-right: 0;\n  }\n}\n\n.list-header {\n  color: $gray-600;\n  font-size: 15px;\n  font-weight: 700;\n  padding: 10px 4px;\n}\n\n.list-seperator {\n  background-color: $card-border-color;\n  height: 1px;\n  margin: 15px 0 9px;\n}\n\n.list-link {\n  > a {\n    color: $gray-600;\n    padding: 4px;\n\n    &:hover {\n      color: $gray-900;\n    }\n  }\n}\n\n// User block\n.user-block {\n  float: left;\n\n  img {\n    float: left;\n    height: 40px;\n    width: 40px;\n  }\n\n  .username,\n  .description,\n  .comment {\n    display: block;\n    margin-left: 50px;\n  }\n\n  .username {\n    font-size: 16px;\n    font-weight: 600;\n    margin-top: -1px;\n  }\n\n  .description {\n    color: $gray-600;\n    font-size: 13px;\n    margin-top: -3px;\n  }\n\n  &.user-block-sm {\n    img {\n      width: $img-size-sm;\n      height: $img-size-sm;\n    }\n\n    .username,\n    .description,\n    .comment {\n      margin-left: 40px;\n    }\n\n    .username {\n      font-size: 14px;\n    }\n  }\n}\n\n// Image sizes\n.img-sm,\n.img-md,\n.img-lg {\n  float: left;\n}\n\n.img-sm {\n  height: $img-size-sm;\n  width: $img-size-sm;\n\n  + .img-push {\n    margin-left: $img-size-sm + $img-size-push;\n  }\n}\n\n.img-md {\n  width: $img-size-md;\n  height: $img-size-md;\n\n  + .img-push {\n    margin-left: $img-size-md + $img-size-push;\n  }\n}\n\n.img-lg {\n  width: $img-size-lg;\n  height: $img-size-lg;\n\n  + .img-push {\n    margin-left: $img-size-lg + $img-size-push;\n  }\n}\n\n// Image bordered\n.img-bordered {\n  border: 3px solid $gray-500;\n  padding: 3px;\n}\n\n.img-bordered-sm {\n  border: 2px solid $gray-500;\n  padding: 2px;\n}\n\n// Rounded and Circle Images\n.img-rounded {\n  @include border-radius($border-radius);\n}\n\n.img-circle {\n  @include border-radius(50%);\n}\n\n// Image sizes\n.img-size-64,\n.img-size-50,\n.img-size-32 {\n  height: auto;\n}\n\n.img-size-64 {\n  width: 64px;\n}\n\n.img-size-50 {\n  width: 50px;\n}\n\n.img-size-32 {\n  width: 32px;\n}\n\n// Block sizes\n.size-32,\n.size-40,\n.size-50 {\n  display: block;\n  text-align: center;\n}\n\n.size-32 {\n  height: 32px;\n  line-height: 32px;\n  width: 32px;\n}\n\n.size-40 {\n  height: 40px;\n  line-height: 40px;\n  width: 40px;\n}\n\n.size-50 {\n  height: 50px;\n  line-height: 50px;\n  width: 50px;\n}\n\n// General attachemnt block\n.attachment-block {\n  background-color: $gray-100;\n  border: 1px solid $card-border-color;\n  margin-bottom: 10px;\n  padding: 5px;\n\n  .attachment-img {\n    float: left;\n    height: auto;\n    max-height: 100px;\n    max-width: 100px;\n  }\n\n  .attachment-pushed {\n    margin-left: 110px;\n  }\n\n  .attachment-heading {\n    margin: 0;\n  }\n\n  .attachment-text {\n    color: $gray-700;\n  }\n}\n\n// Overlays for Card, InfoBox & SmallBox\n.card,\n.overlay-wrapper,\n.info-box,\n.small-box {\n  // Box overlay for LOADING STATE effect\n  > .overlay,\n  > .loading-img {\n    height: 100%;\n    left: 0;\n    position: absolute;\n    top: 0;\n    width: 100%;\n  }\n\n  .overlay {\n    @include border-radius($border-radius);\n    align-items: center;\n    background-color: rgba($white, .7);\n    display: flex;\n    justify-content: center;\n    z-index: 50;\n\n    > .fa,\n    > .fas,\n    > .far,\n    > .fab,\n    > .fal,\n    > .fad,\n    > .svg-inline--fa,\n    > .ion {\n      color: $gray-800;\n    }\n\n    &.dark {\n      background-color: rgba($black, .5);\n\n      > .fa,\n      > .fas,\n      > .far,\n      > .fab,\n      > .fal,\n      > .fad,\n      > .svg-inline--fa,\n      > .ion {\n        color: $gray-400;\n      }\n    }\n  }\n}\n\n.tab-pane {\n  // Box overlay for LOADING STATE effect on Tab Panels\n  > .overlay-wrapper {\n    position: relative;\n    > .overlay {\n      border-top-left-radius: 0;\n      border-top-right-radius: 0;\n      flex-direction: column;\n      margin-top: -$card-spacer-x;\n      margin-left: -$card-spacer-x;\n      height: calc(100% + 2 * #{$card-spacer-x});\n      width: calc(100% + 2 * #{$card-spacer-x});\n\n      &.dark {\n        color: $white;\n      }\n    }\n  }\n}\n\n// Ribbon\n.ribbon-wrapper {\n  height: $ribbon-wrapper-size;\n  overflow: hidden;\n  position: absolute;\n  right: -2px;\n  top: -2px;\n  width: $ribbon-wrapper-size;\n  z-index: 10;\n\n  &.ribbon-lg {\n    height: $ribbon-lg-wrapper-size;\n    width: $ribbon-lg-wrapper-size;\n\n    .ribbon {\n      right: $ribbon-lg-right;\n      top: $ribbon-lg-top;\n      width: $ribbon-lg-width;\n    }\n  }\n\n  &.ribbon-xl {\n    height: $ribbon-xl-wrapper-size;\n    width: $ribbon-xl-wrapper-size;\n\n    .ribbon {\n      right: $ribbon-xl-right;\n      top: $ribbon-xl-top;\n      width: $ribbon-xl-width;\n    }\n  }\n\n  .ribbon {\n    box-shadow: 0 0 $ribbon-border-size rgba($black, .3);\n    font-size: $ribbon-font-size;\n    line-height: $ribbon-line-height;\n    padding: $ribbon-padding;\n    position: relative;\n    right: $ribbon-right;\n    text-align: center;\n    text-shadow: 0 -1px 0 rgba($black, .4);\n    text-transform: uppercase;\n    top: $ribbon-top;\n    transform: rotate(45deg);\n    width: $ribbon-width;\n\n    &::before,\n    &::after {\n      border-left: $ribbon-border-size solid transparent;\n      border-right: $ribbon-border-size solid transparent;\n      border-top: $ribbon-border-size solid #9e9e9e;\n      bottom: -$ribbon-border-size;\n      content: \"\";\n      position: absolute;\n    }\n\n    &::before {\n      left: 0;\n    }\n\n    &::after {\n      right: 0;\n    }\n  }\n}\n\n// Scroll To Top\n.back-to-top {\n  bottom: 1.25rem;\n  position: fixed;\n  right: 1.25rem;\n  z-index: $zindex-control-sidebar + 1;\n\n  &:focus {\n    box-shadow: none;\n  }\n}\n\n// Pre\npre {\n  padding: .75rem;\n}\n\n// Blockquotes styles\nblockquote {\n  background-color: $white;\n  border-left: .7rem solid $primary;\n  margin: 1.5em .7rem;\n  padding: .5em .7rem;\n\n  .box & {\n    background-color: $gray-200;\n  }\n\n  p:last-child {\n    margin-bottom: 0;\n  }\n\n  h1,\n  h2,\n  h3,\n  h4,\n  h5,\n  h6 {\n    color: $primary;\n    font-size: 1.25rem;\n    font-weight: 600;\n  }\n\n  @each $color, $value in $theme-colors {\n    &.quote-#{$color} {\n      border-color: $value;\n\n      h1,\n      h2,\n      h3,\n      h4,\n      h5,\n      h6 {\n        color: $value;\n      }\n    }\n  }\n\n  @each $color, $value in $colors {\n    &.quote-#{$color} {\n      border-color: $value;\n\n      h1,\n      h2,\n      h3,\n      h4,\n      h5,\n      h6 {\n        color: $value;\n      }\n    }\n  }\n}\n\n\n// Tab Custom Content\n\n.tab-custom-content {\n  border-top: $nav-tabs-border-width solid $nav-tabs-border-color;\n  margin-top: .5rem;\n  padding-top: .5rem;\n}\n\n.nav + .tab-custom-content {\n  border-top: none;\n  border-bottom: $nav-tabs-border-width solid $nav-tabs-border-color;\n  margin-top: 0;\n  margin-bottom: .5rem;\n  padding-bottom: .5rem;\n}\n\n\n// Badge BTN Style\n.badge-btn {\n  border-radius: $button-border-radius-xs;\n  font-size: $button-font-size-xs;\n  font-weight: 400;\n  padding: $button-padding-y-xs * 2 $button-padding-x-xs * 2;\n}\n\n.badge-btn.badge-pill {\n  padding: .375rem .6rem;\n}\n\n@include dark-mode () {\n  a:not(.btn):hover {\n    color: lighten($link-color, 10%);\n  }\n  .attachment-block {\n    background-color: lighten($dark, 3.75%);\n\n    .attachment-text {\n      color: $gray-400;\n    }\n  }\n  blockquote {\n    background-color: lighten($dark, 5%);\n\n    @each $color, $value in $theme-colors {\n      &.quote-#{$color} {\n        border-color: $value;\n\n        h1,\n        h2,\n        h3,\n        h4,\n        h5,\n        h6 {\n          color: $value;\n        }\n      }\n    }\n\n    @each $color, $value in $colors {\n      &.quote-#{$color} {\n        border-color: $value;\n\n        h1,\n        h2,\n        h3,\n        h4,\n        h5,\n        h6 {\n          color: $value;\n        }\n      }\n    }\n  }\n  .close,\n  .mailbox-attachment-close {\n    color: $gray-500;\n    text-shadow: 0 1px 0 $gray-700;\n  }\n  .tab-custom-content {\n    border-color: $gray-600;\n  }\n  .list-group-item {\n    background-color: $dark;\n    border-color: $gray-600;\n  }\n}\n","//\n// Misc: Print\n//\n\n@media print {\n  //Add to elements that you do not want to show when printing\n  .no-print {\n    display: none !important;\n  }\n\n  //Elements that we want to hide when printing\n  .main-sidebar,\n  .main-header,\n  .content-header {\n    @extend .no-print;\n  }\n\n  //This is the only element that should appear, so let's remove the margins\n  .content-wrapper,\n  .main-footer {\n    @include translate(0, 0);\n    margin-left: 0 !important;\n    min-height: 0 !important;\n  }\n\n  .layout-fixed .content-wrapper {\n    padding-top: 0 !important;\n  }\n\n  //Invoice printing\n  .invoice {\n    border: 0;\n    margin: 0;\n    padding: 0;\n    width: 100%;\n  }\n\n  .invoice-col {\n    float: left;\n    width: 33.3333333%;\n  }\n\n  //Make sure table content displays properly\n  .table-responsive {\n    overflow: auto;\n\n    > .table tr th,\n    > .table tr td {\n      white-space: normal !important;\n    }\n  }\n}\n","//\n// Component: Text\n//\n\n// text modification\n.text-bold {\n  &,\n  &.table td,\n  &.table th {\n    font-weight: 700;\n  }\n}\n\n.text-xs {\n  font-size: $font-size-xs !important;\n}\n\n.text-sm {\n  font-size: $font-size-sm !important;\n}\n\n.text-md {\n  font-size: $font-size-base !important;\n}\n\n.text-lg {\n  font-size: $font-size-lg !important;\n}\n\n.text-xl {\n  font-size: $font-size-xl !important;\n}\n\n// text color variations\n@each $name, $color in $colors {\n  .text-#{$name} {\n    color: #{$color} !important;\n  }\n}\n\n@include dark-mode () {\n  .text-muted {\n    color: $gray-500 !important;\n  }\n\n  @each $name, $color in $colors-alt {\n    .text-#{$name} {\n      color: #{$color} !important;\n    }\n  }\n}\n","//\n// Component: Elevation\n//\n\n.elevation-0 {\n  box-shadow: none !important;\n}\n\n// Background colors (colors)\n@each $name, $value in $elevations {\n  .elevation-#{$name} {\n    box-shadow: $value !important;\n  }\n}\n","//\n// Mixins: Backgrounds\n//\n\n// Background Variant\n@mixin background-variant($name, $color) {\n  #{if(&, \"&.bg-#{$name}\",\".bg-#{$name}\")} {\n    background-color: #{$color} !important;\n\n    &,\n    > a {\n      color: color-yiq($color) !important;\n    }\n\n    &.btn {\n      &:hover {\n        border-color: darken($color, 10%);\n        color: darken(color-yiq($color), 7.5%);\n      }\n\n      &:not(:disabled):not(.disabled):active,\n      &:not(:disabled):not(.disabled).active,\n      &:active,\n      &.active {\n        background-color: darken($color, 10%) !important;\n        border-color: darken($color, 12.5%);\n        color: color-yiq(darken($color, 10%));\n      }\n    }\n  }\n}\n\n// Background Gradient Variant\n@mixin background-gradient-variant($name, $color) {\n  .bg-gradient-#{$name} {\n    background: $color linear-gradient(180deg, mix($body-bg, $color, 15%), $color) repeat-x !important;\n    color: color-yiq($color);\n\n    &.btn {\n      &:not(:disabled):not(.disabled):active,\n      &:not(:disabled):not(.disabled).active,\n      .show > &.dropdown-toggle {\n        background-image: none !important;\n      }\n\n      &:hover {\n        background: $color linear-gradient(180deg, mix($body-bg, darken($color, 7.5%), 15%), darken($color, 7.5%)) repeat-x !important;\n        border-color: darken($color, 10%);\n        color: darken(color-yiq($color), 7.5%);\n      }\n\n      &:not(:disabled):not(.disabled):active,\n      &:not(:disabled):not(.disabled).active,\n      &:active,\n      &.active {\n        background: $color linear-gradient(180deg, mix($body-bg, darken($color, 10%), 15%), darken($color, 10%)) repeat-x !important;\n        border-color: darken($color, 12.5%);\n        color: color-yiq(darken($color, 10%));\n      }\n\n      &:disabled,\n      &.disabled {\n        background-image: none !important;\n        border-color: $color;\n        color: color-yiq($color);\n      }\n    }\n  }\n}","//\n// Misc: Colors\n//\n\n// Background colors (theme colors)\n@each $name, $color in $theme-colors {\n  @include background-variant($name, $color);\n}\n\n// Background colors (colors)\n@each $name, $color in $colors {\n  @include background-variant($name, $color);\n}\n\n@media print {\n  .table td,\n  .table th {\n    // Background colors (theme colors)\n    @each $name, $color in $theme-colors {\n      @include background-variant($name, $color);\n    }\n\n    // Background colors (colors)\n    @each $name, $color in $colors {\n      @include background-variant($name, $color);\n    }\n  }\n}\n\n.bg-gray {\n  background-color: $gray-500;\n  color: color-yiq($gray-500);\n}\n\n.bg-gray-light {\n  background-color: lighten($gray-200, 3%);\n  color: color-yiq(lighten($gray-200, 3%)) !important;\n}\n\n.bg-black {\n  background-color: $black;\n  color: color-yiq($black) !important;\n}\n\n.bg-white {\n  background-color: $white;\n  color: color-yiq($white) !important;\n}\n\n// Gradient Background colors (theme colors)\n@each $name, $color in $theme-colors {\n  @include background-gradient-variant($name, $color);\n}\n\n// Gradient Background colors (colors)\n@each $name, $color in $colors {\n  @include background-gradient-variant($name, $color);\n}\n\n// Backgrund Color Disabled\n[class^=\"bg-\"].disabled {\n  opacity: .65;\n}\n\n// Text muted hover\na.text-muted:hover {\n  color: theme-color(primary) !important;\n}\n\n// Link Styles\n.link-muted {\n  color: darken($gray-500, 30%);\n\n  &:hover,\n  &:focus {\n    color: darken($gray-500, 40%);\n  }\n}\n\n.link-black {\n  color: $gray-600;\n\n  &:hover,\n  &:focus {\n    color: lighten($gray-500, 20%);\n  }\n}\n\n// Accent colors (theme colors)\n@each $name, $color in $theme-colors {\n  @include accent-variant($name, $color);\n}\n\n// Accent colors (colors)\n@each $name, $color in $colors {\n  @include accent-variant($name, $color);\n}\n\n// Accent button override fix\n[class*=\"accent-\"] {\n  @each $name, $color in $theme-colors {\n    a.btn-#{$name} {\n      color: color-yiq($color);\n    }\n  }\n}\n\n@include dark-mode () {\n  .bg-light {\n    background-color: lighten($dark, 7.5%) !important;\n    color: $white !important;\n  }\n  .text-black,\n  .text-dark,\n  .link-black,\n  .link-dark {\n    color: $gray-400 !important;\n  }\n\n  // Background colors (theme colors)\n  @each $name, $color in $theme-colors-alt {\n    @include background-variant($name, $color);\n  }\n\n  // Background colors (colors)\n  @each $name, $color in $colors-alt {\n    @include background-variant($name, $color);\n  }\n\n  // Gradient Background colors (theme colors)\n  @each $name, $color in $theme-colors-alt {\n    @include background-gradient-variant($name, $color);\n  }\n\n  // Gradient Background colors (colors)\n  @each $name, $color in $colors-alt {\n    @include background-gradient-variant($name, $color);\n  }\n\n  // Accent colors (theme colors)\n  @each $name, $color in $theme-colors-alt {\n    @include accent-variant($name, $color);\n  }\n\n  [class*=\"accent-\"] {\